import { WorkThumbnail } from './WorkThumbnail';
import { pageType } from './PageType';
import { Tools } from './Tools';
import { Config } from './Config';
// 
class ArtworkThumbnail extends WorkThumbnail {
    constructor() {
        super();
        this.selectors = [];
        if (Config.mobile) {
            // 
            this.selectors = ['.works-item-illust'];
        }
        else {
            this.selectors = [
                'div[width="136"]',
                'div[width="131"]',
                'div[width="288"]',
                'div[width="184"]',
                'div[width="112"]',
                'div[width="104"]',
                'div[width="90"]',
                'div[width="118"]',
                '._work',
                '._work.item',
                'li>div>div:first-child',
            ];
        }
        this.findThumbnail(document.body);
        this.createObserver(document.body);
    }
    findThumbnail(parent) {
        if (!parent.querySelectorAll) {
            return;
        }
        // 
        // 
        // 
        //  dataset.mouseover 
        // 'li>div>div:first-child' 'div[width="288"]'
        for (const selector of this.selectors) {
            //  'li>div>div:first-child' 
            if (selector === 'li>div>div:first-child' &&
                pageType.type !== pageType.list.Request) {
                return;
            }
            const elements = parent.querySelectorAll(selector);
            for (const el of elements) {
                const id = Tools.findWorkIdFromElement(el, 'illusts');
                if (Config.mobile) {
                    //  id
                    //  A  id
                    //  A 
                    // 
                    //  id
                    this.bindEvents(el, id);
                }
                else {
                    //  id 
                    if (id) {
                        this.bindEvents(el, id);
                    }
                }
            }
        }
    }
}
const artworkThumbnail = new ArtworkThumbnail();
export { artworkThumbnail };
