import { lang } from './Lang';
import { Config } from './Config';
import { msgBox } from './MsgBox';
import { Utils } from './utils/Utils';
import { EVT } from './EVT';
import { setSetting, settings } from './setting/Settings';
// 
class ShowWhatIsNew {
    constructor() {
        this.flag = '15.8.0';
        this.bindEvents();
    }
    bindEvents() {
        window.addEventListener(EVT.list.settingInitialized, () => {
            //  settingInitialized 
            // <strong>${lang.transl('_')}</strong>
            let msg = `
      ${lang.transl('_')}
      <br>
      ${lang.transl('_')}
      `;
            // ${lang.transl(
            //   '_xx',
            //   lang.transl('_')
            // )}
            // 
            msg += `
      <br>
      <br>
      ${lang.transl('_')}`;
            this.show(msg);
        });
    }
    show(msg) {
        if (Utils.isPixiv() && settings.whatIsNewFlag !== this.flag) {
            msgBox.show(msg, {
                title: Config.appName + ` ${lang.transl('_')}`,
                btn: lang.transl('_'),
            });
            setSetting('whatIsNewFlag', this.flag);
        }
    }
}
new ShowWhatIsNew();
