import { settings } from '../setting/Settings';
import { Tools } from '../Tools';
import { Utils } from '../utils/Utils';
import { downloadNovelEmbeddedImage } from './DownloadNovelEmbeddedImage';
class MakeNovelFile {
    static async make(data, type = settings.novelSaveAs) {
        if (type === 'txt') {
            return this.makeTXT(data, settings.saveNovelMeta);
        }
        return this.makeEPUB(data, settings.saveNovelMeta);
    }
    static makeTXT(data, saveMeta = true) {
        let content = saveMeta ? data.meta + data.content : data.content;
        //  html 
        content = content.replace(/<br \/>/g, '\n').replace(/<\/?.+?>/g, '');
        return new Blob([content], {
            type: 'text/plain',
        });
    }
    static makeEPUB(data, saveMeta = true) {
        return new Promise(async (resolve, reject) => {
            let content = saveMeta ? data.meta + data.content : data.content;
            content = Tools.replaceEPUBText(content);
            //  replaceEPUBText  <img> 
            content = await downloadNovelEmbeddedImage.EPUB(content, data.embeddedImages);
            // epub  title  epub 
            const userName = Tools.replaceEPUBText(Utils.replaceUnsafeStr(data.userName));
            const title = Tools.replaceEPUBText(Utils.replaceUnsafeStr(data.title));
            new EpubMaker()
                .withTemplate('idpf-wasteland')
                .withAuthor(userName)
                .withModificationDate(new Date(data.createDate))
                .withRights({
                description: Tools.replaceEPUBText(data.description),
                license: '',
            })
                .withAttributionUrl(`https://www.pixiv.net/novel/show.php?id=${data.id}`)
                .withCover(data.coverUrl, {
                license: '',
                attributionUrl: '',
            })
                .withTitle(title)
                .withSection(new EpubMaker.Section('chapter', null, {
                title: title,
                content: content,
            }, true, true))
                .makeEpub()
                .then((blob) => {
                resolve(blob);
            });
        });
    }
}
export { MakeNovelFile };
