import { API } from './API';
import { lang } from './Lang';
import { log } from './Log';
import { toast } from './Toast';
import { token } from './Token';
import { states } from './store/States';
class UnBookmarkWorks {
    async start(idList) {
        log.warning(lang.transl('_'));
        if (idList.length === 0) {
            toast.error(lang.transl('_'));
            log.error(lang.transl('_'));
            return;
        }
        states.busy = true;
        const total = idList.length.toString();
        log.log(lang.transl('_', total));
        let number = 0;
        for (const idData of idList) {
            try {
                const data = await API[idData.type === 'novels' ? 'getNovelData' : 'getArtworkData'](idData.id);
                if (data.body.bookmarkData) {
                    await API.deleteBookmark(data.body.bookmarkData.id, idData.type === 'novels' ? 'novels' : 'illusts', token.token);
                    //  deleteBookmark  API  429 
                    // 48  96 
                }
            }
            catch (error) {
                //  404 
                // 
                // 
            }
            number++;
            log.log(`${number} / ${total}`, 1, false);
        }
        const msg = lang.transl('_') + ' ' + lang.transl('_');
        log.success(msg);
        toast.success(msg, {
            position: 'topCenter',
        });
        states.busy = false;
    }
}
const unBookmarkWorks = new UnBookmarkWorks();
export { unBookmarkWorks };
