//  P 
import { InitPageBase } from '../crawl/InitPageBase';
import { Colors } from '../Colors';
import { lang } from '../Lang';
import { options } from '../setting/Options';
import { API } from '../API';
import { store } from '../store/Store';
import { log } from '../Log';
import { Tools } from '../Tools';
import { createCSV } from '../utils/CreateCSV';
import { Utils } from '../utils/Utils';
import { states } from '../store/States';
import { Config } from '../Config';
import { setTimeoutWorker } from '../SetTimeoutWorker';
class InitFollowingPage extends InitPageBase {
    constructor() {
        super();
        this.baseOffset = 0; // 
        this.onceNumber = 24; //  24 
        this.pageType = 0; // 
        // 0 
        // 1  P 
        // 2 
        this.getUserListNo = 0; // 
        this.limit = 100; // 
        this.totalNeed = Number.MAX_SAFE_INTEGER;
        this.myId = '';
        this.rest = 'show';
        this.tag = '';
        this.userList = [];
        this.index = 0; // getIdList  userList 
        this.userInfoList = []; //  id 
        this.downUserList = false; // 
        this.homePrefix = 'https://www.pixiv.net/users/'; // 
        this.getPageType();
        this.init();
    }
    getPageType() {
        const pathname = window.location.pathname;
        if (pathname.includes('/following')) {
            this.pageType = 0;
        }
        else if (pathname.includes('/mypixiv')) {
            this.pageType = 1;
        }
        else if (pathname.includes('/followers')) {
            this.pageType = 2;
        }
    }
    addCrawlBtns() {
        Tools.addBtn('crawlBtns', Colors.bgBlue, '_', '_').addEventListener('click', () => {
            this.readyCrawl();
        });
        Tools.addBtn('crawlBtns', Colors.bgGreen, '_').addEventListener('click', () => {
            this.downUserList = true;
            this.readyCrawl();
        });
    }
    setFormOption() {
        // /
        options.setWantPageTip({
            text: '_',
            tip: '_',
            rangTip: '_1',
        });
    }
    getWantPage() {
        this.crawlNumber = this.checkWantPageInput(lang.transl('_x'), lang.transl('_'));
    }
    nextStep() {
        this.setSlowCrawl();
        this.readyGet();
        log.log(lang.transl('_'));
        this.getPageType();
        this.getUserList();
    }
    readyGet() {
        this.rest = location.href.includes('rest=hide') ? 'hide' : 'show';
        this.tag = Utils.getURLPathField(window.location.pathname, 'following');
        // 
        const nowPage = Utils.getURLSearchField(location.href, 'p');
        // 
        if (nowPage !== '') {
            this.baseOffset = (parseInt(nowPage) - 1) * this.onceNumber;
        }
        else {
            this.baseOffset = 0;
        }
        // 
        this.totalNeed = Number.MAX_SAFE_INTEGER;
        if (this.crawlNumber !== -1) {
            this.totalNeed = this.onceNumber * this.crawlNumber;
        }
        //  id
        const test = /users\/(\d*)\//.exec(location.href);
        if (test && test.length > 1) {
            this.myId = test[1];
        }
        else {
            const msg = `Get the user's own id failed`;
            log.error(msg, 2);
            throw new Error(msg);
        }
    }
    // 
    async getUserList() {
        const offset = this.baseOffset + this.getUserListNo * this.limit;
        let res;
        try {
            switch (this.pageType) {
                case 0:
                    res = await API.getFollowingList(this.myId, this.rest, this.tag, offset);
                    break;
                case 1:
                    res = await API.getMyPixivList(this.myId, offset);
                    break;
                case 2:
                    res = await API.getFollowersList(this.myId, offset);
                    break;
            }
        }
        catch (_a) {
            this.getUserList();
            return;
        }
        const users = res.body.users;
        if (users.length === 0) {
            // 
            return this.getUserListComplete();
        }
        for (const userData of users) {
            //  id
            this.userList.push(userData.userId);
            // 
            if (this.downUserList) {
                this.userInfoList.push({
                    userId: userData.userId,
                    userName: userData.userName,
                    homePage: this.homePrefix + userData.userId,
                    userComment: userData.userComment,
                    profileImageUrl: userData.profileImageUrl,
                });
            }
            if (this.userList.length >= this.totalNeed) {
                // 
                return this.getUserListComplete();
            }
        }
        log.log(lang.transl('_x', this.userList.length.toString()), 1, false);
        this.getUserListNo++;
        this.getUserList();
    }
    getUserListComplete() {
        log.log(lang.transl('_x', this.userList.length.toString()));
        if (this.userList.length === 0) {
            return this.getIdListFinished();
        }
        // 
        if (this.downUserList) {
            this.toCSV();
            return this.getIdListFinished();
        }
        this.getIdList();
    }
    toCSV() {
        // 
        const data = this.userInfoList.map((item) => {
            return Object.values(item);
        });
        // 
        data.unshift(Object.keys(this.userInfoList[0]));
        const csv = createCSV.create(data);
        const csvURL = URL.createObjectURL(csv);
        const csvName = Tools.getPageTitle();
        Utils.downloadFile(csvURL, Utils.replaceUnsafeStr(csvName) + '.csv');
    }
    //  id 
    async getIdList() {
        let idList = [];
        try {
            idList = await API.getUserWorksByType(this.userList[this.index]);
        }
        catch (_a) {
            this.getIdList();
            return;
        }
        store.idList = store.idList.concat(idList);
        this.index++;
        log.log(`${lang.transl('_x', this.index.toString())}, ${lang.transl('_', store.idList.length.toString())}`, 1, false);
        if (this.index >= this.userList.length) {
            return this.getIdListFinished();
        }
        if (states.slowCrawlMode) {
            setTimeoutWorker.set(() => {
                this.getIdList();
            }, Config.slowCrawlDealy);
        }
        else {
            this.getIdList();
        }
    }
    resetGetIdListStatus() {
        this.userList = [];
        this.userInfoList = [];
        this.downUserList = false;
        this.getUserListNo = 0;
        this.index = 0;
    }
    sortResult() {
        //  id id 
        store.result.sort(Utils.sortByProperty('id'));
    }
}
export { InitFollowingPage };
