import { Config } from './Config';
import { EVT } from './EVT';
import { Utils } from './utils/Utils';
// 
class Theme {
    constructor() {
        this.allTheme = ['white', 'dark'];
        this.defaultTheme = 'white'; // 
        this.theme = 'white'; // 
        this.settingTheme = ''; // 
        //  className
        //  className
        //  white  className  className
        this.classNameMap = new Map([
            ['white', ''],
            ['dark', 'theme-dark'],
        ]);
        this.selector = '#gtm-var-theme-kind'; // 
        this.timer = 0;
        // 
        this.htmlFlagMap = new Map([
            ['', 'white'],
            ['default', 'white'],
            ['dark', 'dark'],
        ]);
        this.elList = []; // 
        if (Utils.isPixiv()) {
            this.bindEvents();
        }
    }
    bindEvents() {
        // 
        window.addEventListener(EVT.list.settingChange, (ev) => {
            const data = ev.detail.data;
            if (data.name === 'theme') {
                this.settingTheme = data.value;
                this.setTheme(data.value);
            }
        });
        // 
        this.timer = window.setInterval(() => {
            this.findFlag();
        }, 300);
    }
    //  pixiv 
    findFlag() {
        if (Config.mobile) {
            const el = document.body;
            if (el) {
                window.clearInterval(this.timer);
                this.setTheme(this.getThemeFromHtml());
                //  body  class 
                const ob = new MutationObserver(() => {
                    const flag = this.getThemeFromHtml();
                    this.setTheme(flag);
                });
                ob.observe(el, {
                    attributes: true,
                    attributeFilter: ['class'],
                });
            }
        }
        else {
            const el = document.querySelector(this.selector);
            if (el) {
                window.clearInterval(this.timer);
                this.setTheme(this.getThemeFromHtml());
                //  textContent 
                const ob = new MutationObserver((mutationsList) => {
                    for (const item of mutationsList) {
                        if (item.type === 'characterData') {
                            const flag = this.getThemeFromHtml();
                            this.setTheme(flag);
                            break;
                        }
                    }
                });
                ob.observe(el, {
                    characterData: true,
                    subtree: true,
                });
            }
        }
    }
    getThemeFromHtml() {
        if (Config.mobile) {
            // 
            const dark = document.body.classList.contains('dark');
            const pageTheme = dark ? 'dark' : 'white';
            EVT.fire('getPageTheme', pageTheme);
            return pageTheme;
        }
        else {
            // 
            //  pixiv 
            const el = document.querySelector(this.selector);
            if (el) {
                const pageTheme = this.htmlFlagMap.get(el.textContent);
                EVT.fire('getPageTheme', pageTheme);
                return pageTheme || this.defaultTheme;
            }
            //  html 
            //  html  'rgba(0, 0, 0, 0)'
            // "rgb(245, 245, 245)"
            // "rgb(0, 0, 0)"
            const htmlBG = getComputedStyle(document.documentElement)['backgroundColor'];
            if (htmlBG) {
                if (htmlBG.includes('rgb(2')) {
                    return 'white';
                }
                else if (htmlBG.includes('rgb(0')) {
                    return 'dark';
                }
            }
            return this.defaultTheme;
        }
    }
    setTheme(flag) {
        // 
        if (this.allTheme.includes(this.settingTheme)) {
            flag = this.settingTheme;
        }
        let result = 'white'; // 
        // 
        switch (flag) {
            case 'white':
                result = 'white';
                break;
            case 'dark':
                result = 'dark';
                break;
            default:
                //  'auto'
                result = this.getThemeFromHtml() || this.defaultTheme;
                break;
        }
        // 
        if (result !== this.theme) {
            this.theme = result;
            for (const el of this.elList) {
                this.setClass(el);
            }
        }
    }
    // 
    register(el) {
        if (!Utils.isPixiv()) {
            return;
        }
        this.elList.push(el);
        this.setClass(el);
    }
    //  className
    setClass(el) {
        //  className
        for (const className of this.classNameMap.values()) {
            if (el.classList.contains(className)) {
                el.classList.remove(className);
            }
        }
        //  className
        const name = this.classNameMap.get(this.theme);
        name && el.classList.add(name);
    }
}
const theme = new Theme();
export { theme };
