import { API } from './API';
import { lang } from './Lang';
import { log } from './Log';
import { toast } from './Toast';
import { states } from './store/States';
import { bookmark } from './Bookmark';
class RemoveWorksTagsInBookmarks {
    async start(idList) {
        if (idList.length === 0) {
            toast.error(lang.transl('_'));
            log.error(lang.transl('_'));
            return;
        }
        states.busy = true;
        const total = idList.length.toString();
        log.log(lang.transl('_', total));
        let number = 0;
        for (const idData of idList) {
            try {
                const data = await API[idData.type === 'novels' ? 'getNovelData' : 'getArtworkData'](idData.id);
                if (data.body.bookmarkData) {
                    await bookmark.add(idData.id, idData.type === 'novels' ? 'novels' : 'illusts', [], false, data.body.bookmarkData.private, true);
                    //  48 
                    // 96 
                    //  429 
                }
            }
            catch (error) {
                //  404 
                //  429  429 
            }
            number++;
            log.log(`${number} / ${total}`, 1, false);
        }
        const msg = lang.transl('_') + ' ' + lang.transl('_');
        log.success(msg);
        toast.success(msg, {
            position: 'topCenter',
        });
        states.busy = false;
    }
}
const removeWorksTagsInBookmarks = new RemoveWorksTagsInBookmarks();
export { removeWorksTagsInBookmarks };
