//  tag  tag  tag
//  init 
import { Tools } from '../Tools';
import { Colors } from '../Colors';
import { lang } from '../Lang';
import { msgBox } from '../MsgBox';
import { states } from '../store/States';
import { toast } from '../Toast';
import { EVT } from '../EVT';
import { pageType } from '../PageType';
class CrawlTagList {
    constructor() {
        this.bindEventFlag = false;
        this.EnablPage = [pageType.list.ArtworkSearch, pageType.list.NovelSearch];
        this._tagList = [];
        this.storeName = 'crawlTagList';
        this.wrapId = 'crawlTagListInputWrap';
        this.onDownloadComplete = () => {
            window.setTimeout(() => {
                if (states.crawlTagList) {
                    this._tagList.shift();
                    this.tagList = this._tagList;
                    if (this._tagList.length === 0) {
                        states.crawlTagList = false;
                        // 
                        this.showTagListWrap.innerHTML = `<span style="color:${Colors.textSuccess}">${lang.transl('_')}</span>`;
                        return;
                    }
                    // 
                    this.readyCrawl();
                }
            }, 0);
        };
    }
    init() {
        this.addCrawlBtns();
        this.addElement();
        this.bindEvents();
        this.restoreData();
    }
    get tagList() {
        return this._tagList;
    }
    set tagList(val) {
        this._tagList = val;
        this.storeData();
        this.showTagList();
        this.toggleWrap(true);
    }
    addCrawlBtns() {
        Tools.addBtn('crawlBtns', Colors.bgBlue, '_').addEventListener('click', () => {
            EVT.fire('closeCenterPanel');
            this.toggleWrap(true);
            // 
            window.scrollTo(0, 0);
        });
    }
    addElement() {
        const htmlText = `<textarea
      id="crawlTagListTextArea"
      data-xzplaceholder="_"
    ></textarea>
    <p id="crawlTagListTip" data-xztext="_"></p>
    <div id="crawlTagListBtnsWrap">
      <button id="crawlTagListBtn" data-xztext="_"></button>
      <button id="clearTagListBtn" data-xztext="_"></button>
    </div>
    <div id="tagListWrap">
      <p data-xztext="_"></p>
      <ul id="showTagList">
      <ul>
    </div>
  `;
        // 
        const test = document.getElementById(this.wrapId);
        if (test !== null) {
            test.remove();
        }
        const wrap = document.createElement('div');
        wrap.id = this.wrapId;
        wrap.innerHTML = htmlText;
        this.wrap = Tools.insertToHead(wrap);
        lang.register(this.wrap);
        this.input = this.wrap.querySelector('#crawlTagListTextArea');
        this.startCrawlBtn = this.wrap.querySelector('#crawlTagListBtn');
        this.clearCrawlBtn = this.wrap.querySelector('#clearTagListBtn');
        this.showTagListWrap = this.wrap.querySelector('#showTagList');
        this.startCrawlBtn.addEventListener('click', () => {
            this.checkInput();
        });
        this.clearCrawlBtn.addEventListener('click', () => {
            this.clear();
        });
    }
    bindEvents() {
        // 
        if (this.bindEventFlag) {
            return;
        }
        this.bindEventFlag = true;
        window.addEventListener(EVT.list.downloadComplete, this.onDownloadComplete);
        window.addEventListener(EVT.list.crawlEmpty, this.onDownloadComplete);
        // 
        window.addEventListener(EVT.list.pageSwitch, () => {
            if (!this.EnablPage.includes(pageType.type) &&
                this._tagList.length === 0) {
                this.toggleWrap(false);
            }
        });
    }
    checkInput() {
        if (states.busy) {
            return toast.error(lang.transl('_'));
        }
        const value = this.input.value.trim();
        if (value === '') {
            return msgBox.error(lang.transl('_'));
        }
        const array = value.split('\n').filter((val) => val !== '' && val !== ' ');
        if (array.length === 0) {
            return msgBox.error(lang.transl('_'));
        }
        this.tagList = array;
        this.readyCrawl();
    }
    readyCrawl() {
        if (states.busy) {
            return toast.error(lang.transl('_'));
        }
        if (!this.EnablPage.includes(pageType.type)) {
            return msgBox.error(lang.transl('_') +
                '<br>' +
                lang.transl('_'));
        }
        states.crawlTagList = true;
        const tag = this._tagList[0];
        //  title P_title 
        document.title = tag;
        //  url url  p_tag  
        const urlTag = Tools.getTagFromURL();
        const newURL = location.href.replace(encodeURIComponent(urlTag), encodeURIComponent(tag));
        history.pushState({ p_tag: tag }, tag, newURL);
        // 
        EVT.fire('crawlTag', tag);
    }
    //  wrap /
    toggleWrap(flag) {
        if (flag !== undefined) {
            this.wrap.style.display = flag ? 'block' : 'none';
        }
        else {
            const now = this.wrap.style.display;
            this.wrap.style.display = now === 'block' ? 'none' : 'block';
        }
    }
    showTagList() {
        const text = this.tagList.map((val) => `<li>${val}</li>`);
        this.showTagListWrap.innerHTML = text.join('');
    }
    clear() {
        if (this.tagList.length === 0) {
            return;
        }
        const confirm = window.confirm(lang.transl('_'));
        if (confirm) {
            this.tagList = [];
            // states.busy 
            if (states.busy) {
                location.reload();
            }
        }
    }
    //  tagList  tagList 
    storeData() {
        if (this.tagList.length === 0) {
            return localStorage.removeItem(this.storeName);
        }
        localStorage.setItem(this.storeName, JSON.stringify(this.tagList));
    }
    //  tagList 
    restoreData() {
        const str = localStorage.getItem(this.storeName);
        if (!str) {
            return;
        }
        const data = JSON.parse(str);
        if (str.length === 0) {
            return;
        }
        this.tagList = data;
        // 
        this.input.value = this.tagList.map((val) => val).join('\n');
        // 
        // 1. 
        // 2. 
    }
}
const crawlTagList = new CrawlTagList();
export { crawlTagList };
