import { Tools } from './Tools';
import { Colors } from './Colors';
import { lang } from './Lang';
import { EVT } from './EVT';
import { states } from './store/States';
import { toast } from './Toast';
import { msgBox } from './MsgBox';
import { Utils } from './utils/Utils';
import { artworkThumbnail } from './ArtworkThumbnail';
import { novelThumbnail } from './NovelThumbnail';
import { pageType } from './PageType';
import { showHelp } from './ShowHelp';
import { Config } from './Config';
// 
class SelectWork {
    constructor() {
        this.created = false;
        this.selectorId = 'selectWorkEl';
        this.left = 0;
        this.top = 0;
        this.half = 10; // 
        this._start = false;
        this._pause = false;
        this._tempHide = false; //  display
        this.controlBtn = document.createElement('button'); // 
        this.crawlBtn = document.createElement('button'); // 
        this.clearBtn = document.createElement('button'); // 
        this.selectedWorkFlagClass = 'selectedWorkFlag'; //  class
        this.positionValue = ['relative', 'absolute', 'fixed']; // 
        // 
        // root ; js-mount-point-discovery 
        this.worksWrapperSelectorList = [
            '#root',
            '#js-mount-point-discovery',
        ];
        // 
        this.usedWorksWrapperSelector = this.worksWrapperSelectorList[0];
        // 
        this.worksWrapper = document.body;
        this.ob = undefined;
        this.idList = [];
        this.sendCrawl = false; // 
        this.crawled = false; // 
        this.svg = `<svg class="icon" aria-hidden="true">
  <use xlink:href="#icon-select"></use>
</svg>`;
        const unlisted = pageType.type === pageType.list.Unlisted;
        if (!this.created && Utils.isPixiv() && !unlisted) {
            this.created = true;
            this.selector = this.createSelectorEl();
            this.addBtn();
            this.bindEvents();
        }
    }
    get start() {
        return this._start;
    }
    set start(bool) {
        this._start = bool;
        states.selectWork = bool;
        this.updateSelectorEl();
        this.updateControlBtn();
    }
    get pause() {
        return this._pause;
    }
    set pause(bool) {
        this._pause = bool;
        if (bool) {
            states.selectWork = false;
        }
        this.updateSelectorEl();
        this.updateControlBtn();
    }
    get tempHide() {
        return this._tempHide;
    }
    set tempHide(bool) {
        this._tempHide = bool;
        this.updateSelectorEl();
    }
    bindEvents() {
        artworkThumbnail.onClick((el, id, ev) => {
            this.clickThumbnail(el, id, ev, 'illusts');
        });
        novelThumbnail.onClick((el, id, ev) => {
            this.clickThumbnail(el, id, ev, 'novels');
        });
        document.body.addEventListener(Config.mobile ? 'touchend' : 'click', (ev) => {
            this.clickElement(ev.target, ev);
        }, true);
        window.addEventListener(EVT.list.openCenterPanel, () => {
            this.tempHide = true;
        });
        window.addEventListener(EVT.list.closeCenterPanel, () => {
            this.tempHide = false;
        });
        window.addEventListener(EVT.list.crawlComplete, () => {
            if (this.sendCrawl) {
                this.sendCrawl = false;
                this.crawled = true;
            }
        });
        //  Alt + S 
        window.addEventListener('keydown', (ev) => {
            if (ev.altKey && ev.code === 'KeyS') {
                this.controlBtn.click();
            }
        });
        // 
        window.addEventListener('mousemove', (ev) => {
            this.moveEvent(ev);
        }, true);
        // 
        //  window.onbeforeunload  window.addEventListener('beforeunload') 
        // 
        window.onbeforeunload = () => {
            // 
            if (this.idList.length > 0 && !this.crawled) {
                msgBox.error(lang.transl('_'), {
                    btn: lang.transl('_'),
                });
                return false;
            }
        };
        // 
        window.addEventListener(EVT.list.pageSwitch, () => {
            let worksWrapper = null;
            for (const selector of this.worksWrapperSelectorList) {
                worksWrapper = document.querySelector(selector);
                if (worksWrapper) {
                    this.usedWorksWrapperSelector = selector;
                    break;
                }
            }
            this.worksWrapper = worksWrapper || document.body;
        });
        // 
        //  pixiv 
        window.addEventListener(EVT.list.pageSwitch, () => {
            // 
            //  pageSwitch 
            this.ob && this.ob.disconnect();
            this.ob = new MutationObserver(Utils.debounce(() => {
                this.reAddAllFlag();
            }, 300));
            this.ob.observe(this.worksWrapper, {
                childList: true,
                subtree: true,
            });
        });
    }
    clearIdList() {
        //  id  id
        this.removeAllSelectedFlag();
        this.idList = [];
        this.updateCrawlBtn();
    }
    createSelectorEl() {
        const el = document.createElement('div');
        el.id = this.selectorId;
        document.body.appendChild(el);
        return el;
    }
    updateSelectorEl() {
        if (!this.selector) {
            return;
        }
        const show = this.canSelect() && !this.tempHide;
        this.selector.style.display = show ? 'block' : 'none';
        //  style  display  noneChrome 
        // 
        if (show) {
            this.selector.style.left = this.left - this.half + 'px';
            this.selector.style.top = this.top - this.half + 'px';
        }
    }
    addBtn() {
        this.controlBtn = Tools.addBtn('selectWorkBtns', Colors.bgGreen, '_');
        this.controlBtn.setAttribute('title', 'Alt + S');
        this.updateControlBtn();
        this.clearBtn = Tools.addBtn('selectWorkBtns', Colors.bgRed, '_');
        this.clearBtn.style.display = 'none';
        this.clearBtn.addEventListener('click', () => {
            this.clearIdList();
            this.clearBtn.style.display = 'none';
            this.crawlBtn.style.display = 'none';
        });
        this.crawlBtn = Tools.addBtn('selectWorkBtns', Colors.bgBlue, '_');
        this.crawlBtn.style.display = 'none';
        this.crawlBtn.addEventListener('click', (ev) => {
            this.sendDownload();
        });
    }
    // 
    updateControlBtn() {
        if (!this.start) {
            lang.updateText(this.controlBtn, '_');
            this.controlBtn.onclick = (ev) => {
                this.startSelect(ev);
                this.clearBtn.style.display = 'block';
                showHelp.show('tipAltSToSelectWork', lang.transl('_ALTS'));
            };
        }
        else {
            if (!this.pause) {
                lang.updateText(this.controlBtn, '_');
                this.controlBtn.onclick = (ev) => {
                    this.pauseSelect();
                };
            }
            else {
                lang.updateText(this.controlBtn, '_');
                this.controlBtn.onclick = (ev) => {
                    this.startSelect(ev);
                };
            }
        }
    }
    // 
    updateCrawlBtn() {
        this.crawlBtn.style.display = this.start ? 'block' : 'none';
        if (this.idList.length > 0) {
            lang.updateText(this.crawlBtn, '_2', this.idList.length.toString());
            this.clearBtn.style.display = 'block';
        }
        else {
            lang.updateText(this.crawlBtn, '_');
        }
    }
    addId(el, id, type) {
        const index = this.idList.findIndex((item) => {
            return item.id === id && item.type === type;
        });
        //  id
        if (index === -1) {
            this.idList.push({
                id,
                type,
            });
            this.crawled = false;
            this.addSelectedFlag(el, id);
        }
        else {
            // id 
            this.idList.splice(index, 1);
            this.removeSelectedFlag(id);
        }
        this.updateCrawlBtn();
    }
    clickThumbnail(el, id, ev, type) {
        if (!this.canSelect()) {
            return;
        }
        if (!id || id === '0') {
            id = Tools.findWorkIdFromElement(el, type === 'novels' ? 'novels' : 'illusts');
        }
        // 
        ev.preventDefault();
        ev.stopPropagation();
        this.addId(el, id, type);
    }
    clickElement(el, ev) {
        if (!this.canSelect()) {
            return;
        }
        if (!el || el.nodeName !== 'A') {
            return;
        }
        const href = el.href;
        const artworkId = Tools.getIllustId(href);
        if (artworkId) {
            ev.preventDefault();
            //  id clickThumbnail
            ev.stopPropagation();
            this.addId(el.parentElement, artworkId, 'illusts');
            return;
        }
        const novelId = Tools.getNovelId(href);
        if (novelId) {
            ev.preventDefault();
            ev.stopPropagation();
            this.addId(el.parentElement, novelId, 'novels');
            return;
        }
    }
    // 
    // 
    moveEvent(ev) {
        this.left = ev.x;
        this.top = ev.y;
        this.updateSelectorEl();
    }
    //  Esc 
    escEvent(ev) {
        if (ev.code === 'Escape') {
            this.pauseSelect();
        }
    }
    // 
    startSelect(ev) {
        this.start = true;
        if (this.pause) {
            // 
            this.pause = false;
        }
        else {
            // 
            this.clearIdList();
        }
        this.bindEscEvent = this.escEvent.bind(this);
        document.addEventListener('keyup', this.bindEscEvent);
        EVT.fire('closeCenterPanel');
    }
    pauseSelect() {
        this.pause = true;
        this.bindEscEvent &&
            document.removeEventListener('keyup', this.bindEscEvent);
    }
    canSelect() {
        return this.start && !this.pause;
    }
    // 
    sendDownload() {
        this.pauseSelect();
        if (this.idList.length > 0) {
            //  id  id 
            EVT.fire('crawlIdList', Array.from(this.idList));
            this.sendCrawl = true;
            this.crawled = false;
            states.quickCrawl = true;
            toast.show(lang.transl('_'), {
                bgColor: Colors.bgBlue,
            });
        }
        else {
            toast.error(lang.transl('_'));
        }
    }
    // 
    addSelectedFlag(wrap, id) {
        const i = document.createElement('i');
        i.classList.add(this.selectedWorkFlagClass);
        i.dataset.id = id;
        i.innerHTML = this.svg;
        wrap.insertAdjacentElement('afterbegin', i);
        // 
        const position = window.getComputedStyle(wrap)['position'];
        if (!this.positionValue.includes(position)) {
            wrap.style.position = 'relative';
        }
    }
    // 
    reAddAllFlag() {
        if (this.idList.length === 0) {
            return;
        }
        for (const { id, type } of this.idList) {
            if (this.getSelectedFlag(id)) {
                // 
                /**
                 *  https://www.pixiv.net/users/18095070/illustrations
                 *  tag  https://www.pixiv.net/users/18095070/illustrations/%E5%A5%B3%E3%81%AE%E5%AD%90
                 * pixiv 
                 */
                return;
            }
            let el;
            if (type === 'novels') {
                el = document.querySelector(`${this.usedWorksWrapperSelector} a[href="/novel/show.php?id=${id}"]`);
            }
            else {
                el = document.querySelector(`${this.usedWorksWrapperSelector} a[href="/artworks/${id}"]`);
            }
            if (el) {
                // 
                this.addSelectedFlag(el, id);
            }
        }
    }
    getSelectedFlag(id) {
        return document.querySelector(`.${this.selectedWorkFlagClass}[data-id='${id}']`);
    }
    // 
    removeSelectedFlag(id) {
        const el = this.getSelectedFlag(id);
        el && el.remove();
    }
    // 
    removeAllSelectedFlag() {
        for (const item of this.idList) {
            this.removeSelectedFlag(item.id);
        }
    }
}
new SelectWork();
