import { settings } from '../setting/Settings';
import { lang } from '../Lang';
import { msgBox } from '../MsgBox';
import { log } from '../Log';
import { EVT } from '../EVT';
import { states } from '../store/States';
class TimedCrawl {
    constructor() {
        this.time = 0;
        /** */
        // https://developer.mozilla.org/zh-CN/docs/Web/API/setTimeout#%E6%9C%80%E5%A4%A7%E5%BB%B6%E6%97%B6%E5%80%BC
        // max: 2147483647 / 60 / 1000
        this.timeMinuteMax = 35791;
        /** */
        this.crawlBySelf = false;
        this.bindEvents();
    }
    /**
     *
     *  1 
     */
    start(cb) {
        if (settings.timedCrawlInterval > this.timeMinuteMax) {
            msgBox.error(lang.transl('_') +
                this.timeMinuteMax +
                lang.transl('_'));
            return;
        }
        if (settings.timedCrawlInterval < 1) {
            msgBox.error(lang.transl('_'));
            return;
        }
        this.reset();
        this.callback = cb;
        this.time = settings.timedCrawlInterval * 60 * 1000;
        this.timer = window.setInterval(() => {
            if (!this.callback) {
                return;
            }
            this.crawlBySelf = true;
            states.quickCrawl = true;
            this.callback();
        }, this.time);
        EVT.fire('startTimedCrawl');
        const msg = lang.transl('_', settings.timedCrawlInterval.toString());
        msgBox.show(msg + '<br><br>' + lang.transl('_2'));
        log.success(msg);
    }
    reset() {
        this.callback = undefined;
        window.clearTimeout(this.timer);
        this.crawlBySelf = false;
        states.quickCrawl = false;
    }
    bindEvents() {
        // 
        const resetCrawlBySelf = [
            EVT.list.crawlEmpty,
            EVT.list.downloadStop,
            EVT.list.downloadPause,
            EVT.list.downloadComplete,
            EVT.list.downloadCancel,
        ];
        for (const ev of resetCrawlBySelf) {
            window.addEventListener(ev, () => {
                window.setTimeout(() => {
                    // 
                    this.crawlBySelf = false;
                }, 50);
            });
        }
        // 
        window.addEventListener(EVT.list.crawlStart, () => {
            if (!this.crawlBySelf) {
                return;
            }
            log.success(lang.transl('_'));
            log.log(lang.transl('_') + new Date().toLocaleString());
        });
        const tipWaitNextCrawl = [EVT.list.crawlEmpty, EVT.list.downloadComplete];
        for (const ev of tipWaitNextCrawl) {
            window.addEventListener(ev, () => {
                window.setTimeout(() => {
                    if (this.crawlBySelf) {
                        log.log(lang.transl('_') + new Date().toLocaleString());
                        log.success(lang.transl('_'));
                    }
                }, 0);
            });
        }
        window.addEventListener(EVT.list.cancelTimedCrawl, () => {
            this.reset();
            const msg = lang.transl('_');
            log.success(msg);
            msgBox.success(msg);
        });
        window.addEventListener(EVT.list.pageSwitch, () => {
            if (!this.callback) {
                return;
            }
            this.reset();
            const msg = lang.transl('_URL');
            log.error(msg);
            msgBox.error(msg);
        });
    }
}
const timedCrawl = new TimedCrawl();
export { timedCrawl };
