// 
class ConvertOldSettings {
    constructor() {
        // 
        // 
        this.data = {
            ratio: {
                '0': 'square',
                '1': 'horizontal',
                '2': 'vertical',
                '3': 'userSet',
            },
            idRange: {
                '1': '>',
                '2': '<',
            },
            widthTag: {
                '1': 'yes',
                '-1': 'no',
            },
            restrict: {
                '1': 'yes',
                '-1': 'no',
            },
            userSetLang: {
                '-1': 'auto',
                '0': 'zh-cn',
                '1': 'ja',
                '2': 'en',
                '3': 'zh-tw',
                '4': 'ko',
            },
        };
    }
    // 
    convert(key, value) {
        const map = this.data[key];
        // 
        if (map) {
            // 
            //  undefined 
            const newValue = map[value];
            if (newValue !== undefined) {
                return newValue;
            }
        }
        return value;
    }
}
const convertOldSettings = new ConvertOldSettings();
export { convertOldSettings };
