import { WorkThumbnail } from './WorkThumbnail';
import { pageType } from './PageType';
import { Tools } from './Tools';
import { Config } from './Config';
// 
class NovelThumbnail extends WorkThumbnail {
    constructor() {
        super();
        this.selectors = [];
        if (Config.mobile) {
            // 
            this.selectors = ['.works-item-novel'];
        }
        else {
            this.selectors = [
                'li[size="1"]>div',
                'section li>div',
                'nav>div>div',
                'div.gtm-novel-work-recommend-link',
                'section ul>div',
                'section ul>li',
                'div._ranking-item',
                'div[size="496"]',
                'li',
            ];
        }
        this.findThumbnail(document.body);
        this.createObserver(document.body);
    }
    findThumbnail(parent) {
        if (!parent.querySelectorAll) {
            return;
        }
        // 
        // 
        // 
        //  dataset.mouseover 
        for (const selector of this.selectors) {
            // 
            if (!Config.mobile) {
                // 
                if (pageType.type === pageType.list.UserHome &&
                    selector !== 'section ul>li' &&
                    selector !== 'li[size="1"]>div') {
                    continue;
                }
                //  div._ranking-item
                if (pageType.type === pageType.list.NovelRanking &&
                    selector !== 'div._ranking-item') {
                    continue;
                }
                //  section ul>li
                if (pageType.type === pageType.list.NovelSeries &&
                    selector !== 'section ul>li') {
                    continue;
                }
                // div.gtm-novel-work-recommend-link 
                if (selector === 'div.gtm-novel-work-recommend-link' &&
                    pageType.type !== pageType.list.Novel) {
                    continue;
                }
            }
            let elements = parent.querySelectorAll(selector);
            // 
            // 
            //  li  li 
            if (pageType.type === pageType.list.NovelSeries &&
                parent.nodeName === 'LI') {
                elements = [parent];
            }
            for (const el of elements) {
                const id = Tools.findWorkIdFromElement(el, 'novels');
                //  '0'
                // 
                if (Config.mobile) {
                    this.bindEvents(el, id);
                }
                else {
                    //  id 
                    if (id) {
                        this.bindEvents(el, id);
                    }
                }
            }
        }
    }
}
const novelThumbnail = new NovelThumbnail();
export { novelThumbnail };
