// 
import { log } from '../Log';
import { lang } from '../Lang';
import { Colors } from '../Colors';
import { Tools } from '../Tools';
import { states } from '../store/States';
import { EVT } from '../EVT';
import { msgBox } from '../MsgBox';
import { Utils } from '../utils/Utils';
import { store } from '../store/Store';
import { toast } from '../Toast';
class DeleteWorks {
    constructor(worksSelectors) {
        this.worksSelector = ''; // 
        this.multipleSelector = ''; // 
        this.ugoiraSelector = ''; // 
        this.delMode = false; // 
        this.delBtn = document.createElement('button');
        this.iconId = 'deleteWorkEl';
        this.left = 0;
        this.top = 0;
        this.half = 12;
        this.deleteWorkCallback = () => { }; //  callback 
        this.worksSelector = worksSelectors;
        this.icon = this.createDeleteIcon();
        this.bindEvents();
    }
    createDeleteIcon() {
        const el = document.createElement('div');
        el.id = this.iconId;
        document.body.appendChild(el);
        return el;
    }
    updateDeleteIcon() {
        if (!this.icon) {
            return;
        }
        this.icon.style.display = this.delMode ? 'block' : 'none';
        // 
        if (this.delMode) {
            this.icon.style.left = this.left - this.half + 'px';
            this.icon.style.top = this.top - this.half + 'px';
        }
    }
    bindEvents() {
        // 
        window.addEventListener(EVT.list.worksUpdate, () => {
            if (this.delMode) {
                this.bindDeleteEvent();
            }
        });
        // 
        window.addEventListener(EVT.list.pageSwitch, () => {
            if (this.delMode) {
                this.toggleDeleteMode();
            }
        });
        // 
        window.addEventListener('mousemove', (ev) => {
            this.moveEvent(ev);
        }, true);
    }
    // 
    moveEvent(ev) {
        this.left = ev.x;
        this.top = ev.y;
        this.updateDeleteIcon();
    }
    // 
    addClearMultipleBtn(selector, callback = () => { }) {
        this.multipleSelector = selector;
        Tools.addBtn('crawlBtns', Colors.bgRed, '_').addEventListener('click', () => {
            if (states.busy) {
                msgBox.error(lang.transl('_'));
                return;
            }
            if (store.resultMeta.length === 0) {
                toast.error(lang.transl('_'));
                return;
            }
            this.clearMultiple();
            callback();
        }, false);
    }
    // 
    addClearUgoiraBtn(selector, callback = () => { }) {
        this.ugoiraSelector = selector;
        Tools.addBtn('crawlBtns', Colors.bgRed, '_').addEventListener('click', () => {
            if (states.busy) {
                msgBox.error(lang.transl('_'));
                return;
            }
            if (store.resultMeta.length === 0) {
                toast.error(lang.transl('_'));
                return;
            }
            this.ClearUgoira();
            callback();
        }, false);
    }
    // 
    addManuallyDeleteBtn(callback = () => { }) {
        this.deleteWorkCallback = callback;
        this.delBtn = Tools.addBtn('crawlBtns', Colors.bgRed, '_', '_Title');
        this.delBtn.addEventListener('click', () => {
            this.toggleDeleteMode();
        });
    }
    // 
    toggleDeleteMode() {
        if (store.resultMeta.length === 0) {
            toast.error(lang.transl('_'));
            return;
        }
        this.delMode = !this.delMode;
        this.bindDeleteEvent();
        this.updateDeleteIcon();
        if (this.delMode) {
            lang.updateText(this.delBtn, '_');
            window.setTimeout(() => {
                EVT.fire('closeCenterPanel');
            }, 100);
        }
        else {
            lang.updateText(this.delBtn, '_');
        }
    }
    // 
    clearMultiple() {
        const allPicArea = document.querySelectorAll(this.worksSelector);
        allPicArea.forEach((el) => {
            if (el.querySelector(this.multipleSelector)) {
                el.remove();
            }
        });
        this.showWorksCount();
    }
    // 
    ClearUgoira() {
        const allPicArea = document.querySelectorAll(this.worksSelector);
        allPicArea.forEach((el) => {
            if (el.querySelector(this.ugoiraSelector)) {
                el.remove();
            }
        });
        this.showWorksCount();
    }
    // 
    // 
    bindDeleteEvent() {
        const listElement = document.querySelectorAll(this.worksSelector);
        listElement.forEach((el) => {
            el.onclick = (ev) => {
                if (this.delMode) {
                    ev.preventDefault();
                    if (states.busy) {
                        msgBox.error(lang.transl('_'));
                        return;
                    }
                    const target = ev.currentTarget;
                    target.remove();
                    this.showWorksCount();
                    this.deleteWorkCallback(target);
                }
            };
        });
    }
    // 
    showWorksCount() {
        const selector = this.worksSelector;
        log.success(lang.transl('_', Utils.getVisibleEl(selector).length.toString()), 2, false);
    }
}
export { DeleteWorks };
