// 
import { InitPageBase } from '../crawl/InitPageBase';
import { Colors } from '../Colors';
import { lang } from '../Lang';
import { options } from '../setting/Options';
import { Tools } from '../Tools';
import { EVT } from '../EVT';
import { Config } from '../Config';
import { toast } from '../Toast';
import { theme } from '../Theme';
class InitHomePage extends InitPageBase {
    constructor() {
        super();
        this.downIdButton = document.createElement('button');
        this.downIdInput = document.createElement('textarea');
        this.ready = false;
        this.init();
        this.idRangeTip = this.createidRangeTip();
    }
    addCrawlBtns() {
        this.downIdButton = Tools.addBtn('crawlBtns', Colors.bgBlue, '_id');
        this.downIdButton.id = 'down_id_button';
        const crawlIdRange = Tools.addBtn('crawlBtns', Colors.bgBlue, '_id');
        crawlIdRange.addEventListener('click', () => {
            this.crawlIdRange();
        });
    }
    addAnyElement() {
        // id
        this.downIdInput.id = 'down_id_input';
        this.downIdInput.style.display = 'none';
        this.downIdInput.setAttribute('data-xzplaceholder', '_id');
        document.body.insertAdjacentElement('beforebegin', this.downIdInput);
        lang.register(this.downIdInput);
        Tools.addBtn('otherBtns', Colors.bgGreen, '_').addEventListener('click', () => {
            EVT.fire('clearSavedCrawl');
        });
    }
    setFormOption() {
        options.hideOption([1]);
    }
    initAny() {
        this.downIdButton.addEventListener('click', () => {
            if (!this.ready) {
                // 
                EVT.fire('closeCenterPanel');
                this.downIdInput.style.display = 'block';
                this.downIdInput.focus();
                document.documentElement.scrollTop = 0;
            }
            else {
                this.checkIdList();
            }
        }, false);
        // 
        this.downIdInput.addEventListener('change', () => {
            if (this.downIdInput.value !== '') {
                this.ready = true;
                window.setTimeout(() => {
                    EVT.fire('openCenterPanel');
                }, 300);
                lang.updateText(this.downIdButton, '_');
            }
            else {
                this.ready = false;
                EVT.fire('closeCenterPanel');
                lang.updateText(this.downIdButton, '_id');
            }
        });
    }
    //  id 
    createidRangeTip() {
        const div = document.createElement('div');
        div.classList.add('id_range_tip', 'beautify_scrollbar', 'logWrap');
        theme.register(div);
        return document.body.insertAdjacentElement('beforebegin', div);
    }
    //  id 
    checkIdList() {
        //  store 
        const array = this.downIdInput.value.split('\n');
        const result = [];
        for (const str of array) {
            const id = parseInt(str);
            if (isNaN(id) || id < 22 || id > Config.worksNumberLimit) {
                console.log(lang.transl('_id') + ': ' + str);
            }
            else {
                result.push(id.toString());
            }
        }
        this.addIdList(result);
    }
    crawlIdRange() {
        let start = 0;
        let end = 0;
        // 
        const startInput = window.prompt(lang.transl('_id') + '\n' + lang.transl('_id'), '0');
        if (startInput) {
            const num = Number.parseInt(startInput);
            if (!isNaN(num) && num >= 0) {
                start = num;
            }
            else {
                return toast.error(lang.transl('_'));
            }
        }
        else {
            return;
        }
        // 
        const endInput = window.prompt(lang.transl('_id'), '1');
        if (endInput) {
            const num = Number.parseInt(endInput);
            if (!isNaN(num) && num > start) {
                end = num;
            }
            else {
                return toast.error(lang.transl('_'));
            }
        }
        else {
            return;
        }
        // 
        const tip = lang.transl('_id') + `: ${start} - ${end}`;
        this.idRangeTip.textContent = tip;
        this.idRangeTip.style.display = 'block';
        //  log.log 
        //  id 
        const ids = [];
        while (start <= end) {
            ids.push(start.toString());
            start++;
        }
        this.addIdList(ids);
    }
    //  id  store 
    addIdList(ids) {
        //  id 
        const type = window.location.pathname === '/novel/' ? 'novels' : 'unknown';
        const idList = [];
        for (const id of ids) {
            idList.push({
                type: type,
                id: id,
            });
        }
        EVT.fire('crawlIdList', idList);
    }
    destroy() {
        Tools.clearSlot('crawlBtns');
        Tools.clearSlot('otherBtns');
        this.downIdInput.remove();
    }
}
export { InitHomePage };
