import { API } from '../API';
import { Tools } from '../Tools';
import { EVT } from '../EVT';
import { lang } from '../Lang';
import { settings, setSetting } from './Settings';
import { theme } from '../Theme';
import { toast } from '../Toast';
import { msgBox } from '../MsgBox';
import { log } from '../Log';
// ,
class DoNotDownloadLastFewImages {
    constructor() {
        this.slotName = 'DoNotDownloadLastFewImagesSlot';
        this._addWrapShow = false;
        this.wrapHTML = `
  <span class="DoNotDownloadLastFewImagesWarp">

    <span class="controlBar">
    <span class="total">0</span>
      <button type="button" class="textButton expand" data-xztext="_"></button>
      <button type="button" class="textButton showAdd" data-xztext="_"></button>
    </span>

    <div class="addWrap">
      <div class="settingItem addInputWrap" >
        <div class="inputItem uid">
          <span class="label uidLabel" data-xztext="_id"></span>
          <input type="text" class="setinput_style1 blue addUidInput" data-xzplaceholder="_" />
        </div>

        <div class="inputItem value">
          <span class="label nameLabel" data-xztext="_"></span>
          <input type="text" class="has_tip setinput_style1 blue addValueInput" data-xztip="_0" />
        </div>

        <div class="btns">
          <button type="button" class="textButton add" data-xztitle="_">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-wanchengqueding"></use>
            </svg>
          </button>

          
          <button type="button" class="textButton cancel" data-xztitle="_">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-guanbiquxiao"></use>
            </svg>
          </button>
        </div>
      </div>
    </div>

    <div class="listWrap">
    </div>
  </span>
  `;
        this.createWrap();
        theme.register(this.wrap);
        lang.register(this.wrap);
        this.bindEvents();
    }
    set addWrapShow(val) {
        this._addWrapShow = val;
        if (val) {
            this.addWrap.style.display = 'block';
            this.addInputUid.focus();
        }
        else {
            this.addWrap.style.display = 'none';
            this.addInputUid.value = '';
            this.addValueInput.value = '';
        }
    }
    get addWrapShow() {
        return this._addWrapShow;
    }
    //  html
    createWrap() {
        this.wrap = Tools.useSlot(this.slotName, this.wrapHTML);
        this.expandBtn = this.wrap.querySelector('.expand');
        this.showAddBtn = this.wrap.querySelector('.showAdd');
        this.totalSpan = this.wrap.querySelector('.total');
        this.addWrap = this.wrap.querySelector('.addWrap');
        this.addInputUid = this.wrap.querySelector('.addUidInput');
        this.addValueInput = this.wrap.querySelector('.addValueInput');
        this.addBtn = this.wrap.querySelector('.add');
        this.cancelBtn = this.wrap.querySelector('.cancel');
        this.listWrap = this.wrap.querySelector('.listWrap');
        // /
        this.expandBtn.addEventListener('click', () => {
            setSetting('DoNotDownloadLastFewImagesShow', !settings.DoNotDownloadLastFewImagesShow);
        });
        // 
        this.showAddBtn.addEventListener('click', () => {
            this.addWrapShow = !this.addWrapShow;
        });
        // 
        this.addBtn.addEventListener('click', () => {
            this.addRule(this.addInputUid.value, this.addValueInput.value);
        });
        // 
        this.cancelBtn.addEventListener('click', () => {
            this.addWrapShow = false;
        });
    }
    bindEvents() {
        window.addEventListener(EVT.list.settingChange, (ev) => {
            const data = ev.detail.data;
            if (data.name === 'DoNotDownloadLastFewImagesShow') {
                this.showListWrap();
            }
            if (data.name === 'DoNotDownloadLastFewImagesList') {
                this.createAllList();
            }
        });
    }
    showListWrap() {
        const show = settings.DoNotDownloadLastFewImagesShow;
        this.listWrap.style.display = show ? 'flex' : 'none';
        lang.updateText(this.expandBtn, show ? '_' : '_');
    }
    //  html
    createAllList() {
        this.totalSpan.textContent =
            settings.DoNotDownloadLastFewImagesList.length.toString();
        this.listWrap.innerHTML = '';
        const df = document.createDocumentFragment();
        for (const { uid, user, value, } of settings.DoNotDownloadLastFewImagesList) {
            df.append(this.createOneList(uid, user, value));
        }
        this.listWrap.append(df);
    }
    // 
    createOneList(uid, user, value) {
        const html = `
      <div class="inputItem user">
        <span>${user}</span>
      </div>

      <div class="inputItem uid">
        <input type="text" class="setinput_style1 blue" data-uidInput="${uid}" value="${uid}" />
      </div>

      <div class="inputItem value">
        <input type="text" class="has_tip setinput_style1 blue" data-valueInput="${uid}" value="${value}" data-xztip="_0" />
      </div>

      <div class="btns">
        <button type="button" class="textButton" data-updateRule="${uid}" data-xztitle="_">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-gengxin"></use>
          </svg>
        </button>

        <button type="button" class="textButton" data-deleteRule="${uid}" data-xztitle="_">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-shanchu1"></use>
          </svg>
        </button>
    </div>`;
        const element = document.createElement('div');
        element.classList.add('settingItem');
        element.dataset.key = uid.toString();
        element.innerHTML = html;
        const updateRule = element.querySelector(`button[data-updateRule='${uid}']`);
        const deleteRule = element.querySelector(`button[data-deleteRule='${uid}']`);
        const uidInput = element.querySelector(`input[data-uidInput='${uid}']`);
        const valueInput = element.querySelector(`input[data-valueInput='${uid}']`);
        [uidInput, valueInput].forEach((el) => {
            el === null || el === void 0 ? void 0 : el.addEventListener('change', () => {
                if (el.value) {
                    this.updateRule(uid, uidInput.value, valueInput.value, false);
                }
            });
        });
        // 
        updateRule === null || updateRule === void 0 ? void 0 : updateRule.addEventListener('click', () => {
            this.updateRule(uid, uidInput.value, valueInput.value);
        });
        // 
        deleteRule === null || deleteRule === void 0 ? void 0 : deleteRule.addEventListener('click', () => {
            this.deleteRule(uid);
        });
        return element;
    }
    // 
    checkValue(uidInput, value) {
        if (!uidInput || !value) {
            msgBox.error(lang.transl('_'));
            return false;
        }
        const uid = Number.parseInt(uidInput);
        if (!uid || isNaN(uid)) {
            msgBox.error(lang.transl('_ID'));
            return false;
        }
        // value  0
        const val = Number.parseInt(value);
        if (isNaN(val) || val < 0) {
            msgBox.error(lang.transl('_') + ' ' + lang.transl('_'));
            return false;
        }
        return {
            uid,
            val,
        };
    }
    async getUserName(uid) {
        return new Promise(async (resolve) => {
            const profile = await API.getUserProfile(uid.toString()).catch((err) => {
                console.log(err);
                log.error(`ERROR: userID ${uid}, status ${err.status}<br><a href="https://www.pixiv.net/users/${uid}" target="_blank">https://www.pixiv.net/users/${uid}</a>`);
            });
            if (profile && profile.body.name) {
                return resolve(profile.body.name);
            }
            return resolve('');
        });
    }
    // 
    async addRule(uid, value) {
        const check = this.checkValue(uid, value);
        if (!check) {
            return;
        }
        let old = settings.DoNotDownloadLastFewImagesList.find((item) => item.uid === check.uid);
        if (old) {
            old.value = check.val;
        }
        else {
            const user = await this.getUserName(check.uid);
            const data = {
                uid: check.uid,
                user: user,
                value: check.val,
            };
            settings.DoNotDownloadLastFewImagesList.push(data);
        }
        setSetting('DoNotDownloadLastFewImagesList', settings.DoNotDownloadLastFewImagesList);
        this.addWrapShow = false;
        toast.success(lang.transl('_'));
    }
    // 
    // tip 
    async updateRule(oldUid, uid, value, tip = true) {
        const check = this.checkValue(uid, value);
        if (!check) {
            return;
        }
        let old = settings.DoNotDownloadLastFewImagesList.find((item) => item.uid === oldUid);
        if (old) {
            //  uid 
            const user = await this.getUserName(check.uid);
            old.uid = check.uid;
            old.user = user;
            old.value = check.val;
        }
        else {
            return;
        }
        setSetting('DoNotDownloadLastFewImagesList', settings.DoNotDownloadLastFewImagesList);
        this.addWrapShow = false;
        if (tip) {
            toast.success(lang.transl('_'));
        }
    }
    // 
    deleteRule(uid) {
        let index = settings.DoNotDownloadLastFewImagesList.findIndex((item) => item.uid === uid);
        if (index > -1) {
            settings.DoNotDownloadLastFewImagesList.splice(index, 1);
        }
        else {
            return;
        }
        setSetting('DoNotDownloadLastFewImagesList', settings.DoNotDownloadLastFewImagesList);
        this.removeListElement(uid);
    }
    removeListElement(uid) {
        const listElement = this.listWrap.querySelector(`.settingItem[data-key='${uid}']`);
        listElement === null || listElement === void 0 ? void 0 : listElement.remove();
    }
}
new DoNotDownloadLastFewImages();
