import { API } from './API';
import { Config } from './Config';
import { EVT } from './EVT';
import { lang } from './Lang';
import { log } from './Log';
import { setTimeoutWorker } from './SetTimeoutWorker';
import { settings } from './setting/Settings';
import { toast } from './Toast';
import { token } from './Token';
import { Tools } from './Tools';
//  API.addBookmark 
class Bookmark {
    constructor() {
        /**  */
        this.taskID = 0;
        /** add  needWait  true  */
        this.nextTaskID = 1;
        window.addEventListener(EVT.list.downloadComplete, () => {
            if (this.taskID > this.nextTaskID) {
                const msg = lang.transl('_');
                log.warning(msg);
                toast.warning(msg, {
                    position: 'center',
                });
            }
        });
        // 
        //  window.onbeforeunload 
        //  SelectWork 
    }
    async getWorkData(type, id) {
        return type === 'illusts'
            ? await API.getArtworkData(id)
            : await API.getNovelData(id);
    }
    /**
     *
     *  tags tag 
     *
     *  tags tags
     *
     *  needAddTag tag settings.widthTagBoolean
     *
     *  restrictfalse true  settings.restrictBoolean
     *
     *  needWait false  true  429  true
     */
    async add(id, type, tags, needAddTag, restrict, needWait) {
        return new Promise(async (resolve, reject) => {
            const _needAddTag = needAddTag === undefined ? settings.widthTagBoolean : !!needAddTag;
            if (_needAddTag) {
                //  tags
                if (tags === undefined) {
                    //  tags tags
                    const data = await this.getWorkData(type, id);
                    tags = Tools.extractTags(data);
                }
            }
            else {
                //  tags
                tags = [];
            }
            const _restrict = restrict === undefined ? settings.restrictBoolean : !!restrict;
            // 
            if (!needWait) {
                const status = await this.sendRequest(id, type, tags, _restrict);
                return resolve(status);
            }
            // 
            const NO = ++this.taskID;
            await this.waitCallMe(NO);
            setTimeoutWorker.set(async () => {
                const status = await this.sendRequest(id, type, tags, _restrict);
                this.nextTaskID++;
                return resolve(status);
            }, Config.slowCrawlDealy);
        });
    }
    async waitCallMe(NO) {
        return new Promise(async (resolve) => {
            if (this.nextTaskID === NO) {
                return resolve(NO);
            }
            else {
                setTimeoutWorker.set(() => {
                    return resolve(this.waitCallMe(NO));
                }, 300);
            }
        });
    }
    async sendRequest(id, type, tags, hide) {
        return new Promise(async (resolve) => {
            API.addBookmark(id, type, tags, hide, token.token).then(async (res) => {
                switch (res.status) {
                    case 400:
                        await token.reset();
                        return resolve(this.sendRequest(id, type, tags, hide));
                    case 429:
                    case 500:
                        toast.error(lang.transl('_'), {
                            position: 'center',
                        });
                        log.error(`${Tools.createWorkLink(id, type === 'illusts')} ${lang.transl('_')}. ${lang.transl('_')}${res.status}. ${lang.transl('_')}`);
                        window.setTimeout(() => {
                            return resolve(this.sendRequest(id, type, tags, hide));
                        }, Config.retryTime);
                        break;
                    default:
                        return resolve(res.status);
                }
            });
        });
    }
}
const bookmark = new Bookmark();
export { bookmark };
