//  csv 
// csv  https://www.jianshu.com/p/54b3afc06126
const example = [
    ['titleA', 'titleB', 'titleC'],
    ['a1', 'b1', 'c1'],
    [[1, 2, 3], false, 456],
    [undefined, 'b,b,b', 'c c c'],
];
// any String String
// 
// 
//  Array1. 2. Symbol 
class CreateCSV {
    constructor() {
        this.separate = ','; // 
        this.CRLF = '\r\n'; // 
        this.reg = / |\"|,|\n/; // 
        this.utf8BOM = this.UTF8BOM();
    }
    // 
    // 
    // 
    format(arr) {
        const rowData = [];
        // 
        for (let i = 0; i < arr.length; i++) {
            let colData = '';
            const origin = arr[i];
            const type = typeof origin;
            // 
            switch (type) {
                case 'string':
                    colData = origin;
                    break;
                case 'bigint':
                case 'number':
                case 'boolean':
                case 'function':
                    colData = origin.toString();
                    break;
                case 'undefined':
                    colData = '';
                    break;
                case 'symbol':
                    colData = origin.description || '';
                    break;
                case 'object':
                    if (origin === null) {
                        colData = '';
                    }
                    else if (Array.isArray(origin)) {
                        colData = origin.join(this.separate);
                    }
                    break;
                default:
                    throw new Error('Unkown data type');
            }
            // 
            colData = colData.replace(/\"/g, '""');
            // 
            if (this.reg.test(colData)) {
                colData = this.addQuotation(colData);
            }
            rowData.push(colData);
        }
        return rowData;
    }
    create(data) {
        // 
        const result = [];
        //  utf8BOM
        result.push(this.utf8BOM);
        // 
        for (const row of data) {
            result.push(this.format(row).join(this.separate));
            result.push(this.CRLF);
        }
        const csvBlob = new Blob(result, {
            type: 'text/csv',
        });
        return csvBlob;
    }
    UTF8BOM() {
        const buff = new ArrayBuffer(3);
        const data = new DataView(buff);
        data.setInt8(0, 0xef);
        data.setInt8(1, 0xbb);
        data.setInt8(2, 0xbf);
        return buff;
    }
    // 
    addQuotation(str) {
        return '"' + str + '"';
    }
}
const createCSV = new CreateCSV();
// createCSV.create(example)
export { createCSV };
