import { API } from '../API';
import { lang } from '../Lang';
import { msgBox } from '../MsgBox';
//  Pixiv / tag
class Mute {
    constructor() {
        this.userList = [];
        this.tagList = [];
        this.got = false; // 
    }
    /** user id  */
    async checkUser(id) {
        // 
        if (!this.got) {
            await this.getMuteSettings();
        }
        if (typeof id === 'number') {
            id = id.toString();
        }
        return this.userList.includes(id);
    }
    /** tag  */
    async checkTag(tag) {
        if (!this.got) {
            await this.getMuteSettings();
        }
        return this.tagList.includes(tag);
    }
    // 
    //  check 
    // 
    async getMuteSettings() {
        this.userList = [];
        this.tagList = [];
        return new Promise(async (resolve, reject) => {
            try {
                const response = await API.getMuteSettings();
                const items = response.body.mute_items;
                for (const item of items) {
                    // 
                    if (item.enabled === false) {
                        continue;
                    }
                    if (item.type === 'user') {
                        this.userList.push(item.value);
                    }
                    if (item.type === 'tag') {
                        this.tagList.push(item.value);
                    }
                }
                this.got = true;
                return resolve(items);
            }
            catch (error) {
                if (error.status === 401) {
                    msgBox.error(lang.transl('_401'));
                }
                return reject(error.status);
            }
        });
    }
}
const mute = new Mute();
export { mute };
