import { API } from '../API';
import { Tools } from '../Tools';
import { toast } from '../Toast';
import { bookmark } from '../Bookmark';
import { lang } from '../Lang';
//  tag
class BookmarksAddTag {
    constructor(btn) {
        this.type = 'illusts'; // 
        this.addTagList = []; //  tag 
        this.addIndex = 0; //  tag 
        this.once = 100; // 
        this.btn = btn;
        this.bindEvents();
    }
    bindEvents() {
        this.btn.addEventListener('click', () => {
            // 
            this.addTagList = [];
            this.addIndex = 0;
            this.btn.setAttribute('disabled', 'disabled');
            this.btn.textContent = `Checking`;
            if (window.location.pathname.includes('/novel')) {
                this.type = 'novels';
            }
            this.readyAddTag();
        });
    }
    //  tagloop 
    async readyAddTag(loop = 0) {
        const offset = loop * this.once; // 
        let errorFlag = false;
        // 
        const [showData, hideData] = await Promise.all([
            API.getBookmarkData(Tools.getUserId(), this.type, '', offset, false),
            API.getBookmarkData(Tools.getUserId(), this.type, '', offset, true),
        ]).catch((error) => {
            if (error.status && error.status === 403) {
                this.btn.textContent = ` Permission denied`;
            }
            errorFlag = true;
            return [];
        });
        if (errorFlag) {
            return;
        }
        // 
        for (const data of [showData, hideData]) {
            const works = data.body.works;
            //  bookmarkData 
            if (works.length > 0 && works[0].bookmarkData) {
                works.forEach((work) => {
                    // 
                    // 404
                    if (work.tags.length > 0) {
                        this.addTagList.push({
                            id: work.id,
                            tags: work.tags,
                            restrict: work.bookmarkData.private,
                        });
                    }
                });
            }
        }
        //  100  100  0  total 
        // 
        const total = offset + this.once;
        if (total >= showData.body.total && total >= hideData.body.total) {
            if (this.addTagList.length === 0) {
                // 
                this.btn.textContent = ` No need`;
                this.btn.removeAttribute('disabled');
                return;
            }
            else {
                //  tag
                this.addTag();
            }
        }
        else {
            // 
            this.readyAddTag(++loop);
        }
    }
    //  tag
    async addTag() {
        const item = this.addTagList[this.addIndex];
        await bookmark.add(item.id, this.type, item.tags, true, item.restrict, true);
        if (this.addIndex < this.addTagList.length - 1) {
            this.addIndex++;
            this.btn.textContent = `${this.addIndex} / ${this.addTagList.length}`;
            // 
            return this.addTag();
        }
        else {
            // 
            this.btn.textContent = ` Complete`;
            this.btn.removeAttribute('disabled');
            toast.success(lang.transl('_'));
        }
    }
}
export { BookmarksAddTag };
