import { API } from '../API';
import { lang } from '../Lang';
import { EVT } from '../EVT';
import { toast } from '../Toast';
import { bookmark } from '../Bookmark';
import { Tools } from '../Tools';
import { log } from '../Log';
import { msgBox } from '../MsgBox';
import { setTimeoutWorker } from '../SetTimeoutWorker';
import { Config } from '../Config';
class BookmarkAllWorks {
    constructor(tipWrap) {
        this.idList = [];
        this.bookmarKData = [];
        this.tipWrap = document.createElement('button');
        if (tipWrap) {
            this.tipWrap = tipWrap;
        }
    }
    //  workList id
    //  type url 
    //  type
    sendWorkList(list, type) {
        this.reset();
        type = type !== null && type !== void 0 ? type : (window.location.pathname.includes('/novel') ? 'novels' : 'illusts');
        const regExp = type === 'illusts' ? /\/artworks\/(\d*)/ : /\?id=(\d*)/;
        for (const el of list) {
            const a = el.querySelector('a');
            if (a) {
                // "https://www.pixiv.net/artworks/82618568"
                // "https://www.pixiv.net/novel/show.php?id=12350618"
                const test = regExp.exec(a.href);
                if (test && test.length > 1) {
                    this.idList.push({
                        type,
                        id: test[1],
                    });
                }
            }
        }
        this.startBookmark();
    }
    //  id 
    sendIdList(list) {
        this.reset();
        this.idList = list;
        this.startBookmark();
    }
    reset() {
        this.idList = [];
        this.bookmarKData = [];
    }
    // 
    async startBookmark() {
        if (this.idList.length === 0) {
            toast.error(lang.transl('_'));
            return;
        }
        this.tipWrap.textContent = `Checking`;
        this.tipWrap.setAttribute('disabled', 'disabled');
        await this.getTagData();
        await this.addBookmarkAll();
        this.complete();
    }
    //  tag 
    async getTagData() {
        return new Promise(async (resolve, reject) => {
            for (const id of this.idList) {
                this.tipWrap.textContent = `Get data ${this.bookmarKData.length} / ${this.idList.length}`;
                try {
                    //  429 
                    await new Promise(async (res) => {
                        setTimeoutWorker.set(async () => {
                            let data;
                            if (id.type === 'novels') {
                                data = await API.getNovelData(id.id);
                            }
                            else {
                                data = await API.getArtworkData(id.id);
                            }
                            this.bookmarKData.push({
                                type: id.type,
                                id: data.body.id,
                                tags: Tools.extractTags(data),
                                restrict: false,
                            });
                            res(id);
                        }, this.idList.length >= 120 ? Config.slowCrawlDealy : 0);
                    });
                }
                catch (error) {
                    //  429  3 
                    const e = error;
                    let msg = '';
                    if (e.status) {
                        msg = `${lang.transl('_')}${lang.transl('_')}${e.status}. ${lang.transl('_')}`;
                    }
                    else {
                        msg = `${lang.transl('_')}${lang.transl('_')}${lang.transl('_')}`;
                    }
                    // 
                    log.error(msg);
                    msgBox.error(msg);
                    this.tipWrap.textContent = ` Error`;
                    this.tipWrap.removeAttribute('disabled');
                    EVT.fire('bookmarkModeEnd');
                    return reject();
                }
            }
            resolve();
        });
    }
    //  tag  tag
    async addBookmarkAll() {
        return new Promise(async (resolve) => {
            let index = 0;
            for (const data of this.bookmarKData) {
                this.tipWrap.textContent = `Add bookmark ${index} / ${this.bookmarKData.length}`;
                await bookmark.add(data.id, data.type, data.tags, undefined, undefined, true);
                index++;
            }
            resolve();
        });
    }
    complete() {
        this.tipWrap.textContent = ` Complete`;
        this.tipWrap.removeAttribute('disabled');
        toast.success(lang.transl('_'));
        EVT.fire('bookmarkModeEnd');
    }
}
export { BookmarkAllWorks };
