// 
import { InitPageBase } from '../crawl/InitPageBase';
import { Colors } from '../Colors';
import { lang } from '../Lang';
import { options } from '../setting/Options';
import { API } from '../API';
import { store } from '../store/Store';
import { EVT } from '../EVT';
import { log } from '../Log';
import { Tools } from '../Tools';
import { states } from '../store/States';
import '../pageFunciton/SaveAvatarIcon';
import '../pageFunciton/SaveAvatarImage';
import '../pageFunciton/SaveUserCover';
import { BookmarkAllWorks } from '../pageFunciton/BookmarkAllWorks';
import { Utils } from '../utils/Utils';
import { Config } from '../Config';
var ListType;
(function (ListType) {
    ListType[ListType["UserHome"] = 0] = "UserHome";
    ListType[ListType["Artworks"] = 1] = "Artworks";
    ListType[ListType["Illustrations"] = 2] = "Illustrations";
    ListType[ListType["Manga"] = 3] = "Manga";
    ListType[ListType["Novels"] = 4] = "Novels";
})(ListType || (ListType = {}));
class InitUserPage extends InitPageBase {
    constructor() {
        super();
        this.listType = ListType.UserHome; // 
        this.onceNumber = 48; //  48  24 
        this.bookmarkAll = new BookmarkAllWorks();
        this.sendBookmarkIdList = () => {
            if (states.bookmarkMode) {
                //  id  type  illusts  novels
                const list = [];
                for (const data of store.idList) {
                    if (data.type === 'novels') {
                        list.push(data);
                    }
                    else {
                        list.push({
                            type: 'illusts',
                            id: data.id,
                        });
                    }
                }
                store.idList = []; //  id 
                this.bookmarkAll.sendIdList(list);
            }
        };
        this.init();
    }
    // 
    addCrawlBtns() {
        Tools.addBtn('crawlBtns', Colors.bgBlue, '_', '_').addEventListener('click', () => {
            this.readyCrawl();
        });
        this.addStartTimedCrawlBtn(this.readyCrawl.bind(this));
        this.addCancelTimedCrawlBtn();
    }
    addAnyElement() {
        Tools.addBtn('otherBtns', Colors.bgGreen, '_').addEventListener('click', () => {
            EVT.fire('saveAvatarImage');
        });
        Tools.addBtn('otherBtns', Colors.bgGreen, '_', '_').addEventListener('click', () => {
            EVT.fire('saveAvatarIcon');
        });
        Tools.addBtn('otherBtns', Colors.bgGreen, '_').addEventListener('click', () => {
            EVT.fire('saveUserCover');
        });
        // 
        const bookmarkAllBtn = Tools.addBtn('otherBtns', Colors.bgGreen, '_');
        this.bookmarkAll = new BookmarkAllWorks(bookmarkAllBtn);
        bookmarkAllBtn.addEventListener('click', async () => {
            //  id 
            //  id 
            EVT.fire('bookmarkModeStart');
            store.tag = Tools.getTagFromURL();
            this.crawlNumber = 1; //  1 
            this.readyGetIdList();
        });
        window.addEventListener(EVT.list.getIdListFinished, this.sendBookmarkIdList);
    }
    setFormOption() {
        // /
        options.setWantPageTip({
            text: '_',
            tip: '_',
            rangTip: '_1',
        });
    }
    getWantPage() {
        this.crawlNumber = this.checkWantPageInput(lang.transl('_x'), lang.transl('_'));
    }
    nextStep() {
        this.readyGetIdList();
        log.log(lang.transl('_'));
    }
    readyGetIdList() {
        // 
        //  pathname  id 
        const test = location.pathname.match(/\/users\/\d+(\/.+)/);
        if (test === null) {
            // 
            this.listType = ListType.UserHome;
        }
        else if (test.length === 2) {
            const str = test[1]; // id 
            if (str.includes('/artworks')) {
                // 
                this.listType = ListType.Artworks;
            }
            else if (str.includes('/illustrations')) {
                // 
                this.listType = ListType.Illustrations;
            }
            else if (str.includes('/manga')) {
                // 
                this.listType = ListType.Manga;
            }
            else if (str.includes('/novels')) {
                // 
                this.listType = ListType.Novels;
                this.onceNumber = 24; //  24 
            }
        }
        store.tag ? this.getIdListByTag() : this.getIdList();
    }
    getOffset() {
        const nowPage = Utils.getURLSearchField(location.href, 'p'); //  1 
        let offset = 0;
        if (nowPage) {
            offset = (parseInt(nowPage) - 1) * this.onceNumber;
        }
        if (offset < 0) {
            offset = 0;
        }
        return offset;
    }
    // 
    getRequsetNumber() {
        let requsetNumber = Config.worksNumberLimit;
        if (this.crawlNumber !== -1) {
            requsetNumber = this.onceNumber * this.crawlNumber;
        }
        return requsetNumber;
    }
    //  id 
    async getIdList() {
        let type = [];
        switch (this.listType) {
            case ListType.UserHome:
                type = ['illusts', 'manga', 'novels'];
                break;
            case ListType.Artworks:
                type = ['illusts', 'manga'];
                break;
            case ListType.Illustrations:
                type = ['illusts'];
                break;
            case ListType.Manga:
                type = ['manga'];
                break;
            case ListType.Novels:
                type = ['novels'];
                break;
        }
        let idList = await API.getUserWorksByType(Tools.getUserId(), type);
        //  24 
        const allWorkIsNovels = idList.every((data) => {
            return data.type === 'novels';
        });
        allWorkIsNovels && (this.onceNumber = 24);
        // 
        const offset = this.getOffset();
        const requsetNumber = this.getRequsetNumber();
        //  id 
        idList.sort(Utils.sortByProperty('id')).reverse();
        //  tag  API 
        //  offset  id
        idList.splice(idList.length - offset, idList.length);
        //  requsetNumber  id
        if (idList.length > requsetNumber) {
            idList.splice(0, idList.length - requsetNumber);
        }
        // 
        store.idList = store.idList.concat(idList);
        this.getIdListFinished();
    }
    //  id  tag
    async getIdListByTag() {
        //  tag
        let type = 'illustmanga';
        switch (this.listType) {
            case ListType.Artworks:
                type = 'illustmanga';
                break;
            case ListType.Illustrations:
                type = 'illusts';
                break;
            case ListType.Manga:
                type = 'manga';
                break;
            case ListType.Novels:
                type = 'novels';
                break;
        }
        // 
        let offset = this.getOffset();
        // 
        const requsetNumber = this.getRequsetNumber();
        // 
        const maxRequest = 1000;
        for (const iterator of new Array(maxRequest)) {
            let data = await API.getUserWorksByTypeWithTag(Tools.getUserId(), type, store.tag, offset, this.onceNumber);
            //  illustType 
            if (this.listType === ListType.Novels) {
                const d = data;
                d.body.works.forEach((data) => store.idList.push({
                    type: 'novels',
                    id: data.id,
                }));
            }
            else {
                const d = data;
                d.body.works.forEach((data) => {
                    let type = 'illusts';
                    switch (data.illustType) {
                        case 0:
                            type = 'illusts';
                            break;
                        case 1:
                            type = 'manga';
                            break;
                        case 2:
                            type = 'ugoira';
                            break;
                    }
                    store.idList.push({
                        type,
                        id: data.id,
                    });
                });
            }
            offset += data.body.works.length;
            // 
            //  API 
            if (store.idList.length >= requsetNumber ||
                data.body.works.length < this.onceNumber) {
                return this.getIdListFinished();
            }
        }
    }
    resetGetIdListStatus() {
        this.listType = ListType.UserHome;
    }
    sortResult() {
        //  id id 
        store.result.sort(Utils.sortByProperty('id'));
    }
    destroy() {
        Tools.clearSlot('crawlBtns');
        Tools.clearSlot('otherBtns');
        window.removeEventListener(EVT.list.getIdListFinished, this.sendBookmarkIdList);
    }
}
export { InitUserPage };
