import { store } from '../store/Store';
import { EVT } from '../EVT';
import { lang } from '../Lang';
import { settings } from '../setting/Settings';
import { toast } from '../Toast';
import { Config } from '../Config';
//  url
class ShowURLs {
    constructor() {
        this.bindEvents();
    }
    bindEvents() {
        window.addEventListener(EVT.list.showURLs, () => {
            this.showURLs();
        });
    }
    showURLs() {
        const urls = [];
        const size = settings.imageSize;
        for (const data of store.result) {
            //  url
            //  url 
            if (data.type !== 3) {
                urls.push(data[size]);
            }
        }
        if (store.result.length === 0 || urls.length === 0) {
            return toast.error(lang.transl('_'));
        }
        let result = '';
        if (store.result.length < Config.outputMax) {
            result = urls.join('<br>');
        }
        else {
            result = urls.join('\n');
        }
        EVT.fire('output', {
            content: result,
            title: '_url',
        });
    }
}
new ShowURLs();
