import { lang } from '../Lang';
import { log } from '../Log';
import { EVT } from '../EVT';
import { states } from '../store/States';
import { settings } from '../setting/Settings';
import { blackAndWhiteImage } from './BlackandWhiteImage';
import { mute } from './Mute';
import { blockTagsForSpecificUser } from './BlockTagsForSpecificUser';
import { msgBox } from '../MsgBox';
import { workPublishTime } from './WorkPublishTime';
// 
class Filter {
    constructor() {
        // 
        this.oneDayTime = 24 * 60 * 60 * 1000; // 
        this.minimumTime = 4 * 60 * 60 * 1000; // 
        // 
        this.MiB = 1024 * 1024;
        this.bindEvents();
    }
    // 
    showTip() {
        this.getDownType();
        this.getDownTypeByAge();
        this.getAIWorkType();
        this.getDownTypeByImgCount();
        this.getDownTypeByColor();
        this.getDownTypeByBmked();
        this.getMultiImageWorkImageLimit();
        this.getBMKNum();
        this.getSetWh();
        this.getRatio();
        this.getIdRange();
        this.getPostDate();
        this.getIncludeTag();
        this.getExcludeTag();
        if (states.debut) {
            log.warning(lang.transl('_Title'));
        }
        this.getBlockList();
        this.getSize();
    }
    // 
    //  await 
    //  FilterOption 
    //  undefined 
    //  boolean true false 
    async check(option) {
        // 
        if (!this.checkDownType(option.workType)) {
            return false;
        }
        if (!this.checkDownTypeByAge(option.xRestrict)) {
            return false;
        }
        if (!this.checkAIWorkType(option.aiType)) {
            return false;
        }
        // 
        if (!this.checkPageCount(option.workType, option.pageCount)) {
            return false;
        }
        // 
        if (!this.checkMultiImageWorkImageLimit(option.workType, option.pageCount)) {
            return false;
        }
        // 
        if (!this.checkDownTypeByBmked(option.bookmarkData)) {
            return false;
        }
        // 
        if (!this.checkBMK(option.bookmarkCount, option.createDate)) {
            return false;
        }
        //  tag
        if (!this.checkExcludeTag(option.tags)) {
            return false;
        }
        //  tag
        if (!this.checkIncludeTag(option.tags)) {
            return false;
        }
        // 
        if (!this.checkWidthHeight(option.width, option.height)) {
            return false;
        }
        // 
        if (!this.checkRatio(option.width, option.height)) {
            return false;
        }
        //  id 
        if (!this.checkIdRange(option.id)) {
            return false;
        }
        //  Pixiv 
        if (!(await this.checkMuteUser(option.userId))) {
            return false;
        }
        if (!(await this.checkMuteTag(option.tags))) {
            return false;
        }
        // 
        if (!this.checkBlockList(option.userId)) {
            return false;
        }
        //  tags
        if (!this.checkBlockTagsForSpecificUser(option.userId, option.tags)) {
            return false;
        }
        // 
        if (!this.checkPostDate(option.createDate)) {
            return false;
        }
        // 
        if (!this.checkIdPublishTime(option.id, option.workTypeString)) {
            return false;
        }
        // 
        if (!this.checkDebut(option.yes_rank)) {
            return false;
        }
        // 
        if (!this.checkSize(option.size)) {
            return false;
        }
        // 
        // 
        if (!(await this.checkBlackWhite(option.mini))) {
            return false;
        }
        return true;
    }
    // ---------------- get ----------------
    // 
    getDownType() {
        // 
        if (!settings.downType0 &&
            !settings.downType1 &&
            !settings.downType2 &&
            !settings.downType3) {
            this.showWarning(lang.transl('_'));
        }
        const tips = [];
        !settings.downType0 && tips.push(lang.transl('_'));
        !settings.downType1 && tips.push(lang.transl('_'));
        !settings.downType2 && tips.push(lang.transl('_'));
        !settings.downType3 && tips.push(lang.transl('_'));
        if (tips.length > 0) {
            log.warning(lang.transl('_') + tips.join(', '));
        }
    }
    getDownTypeByAge() {
        // 
        if (!settings.downAllAges && !settings.downR18 && !settings.downR18G) {
            this.showWarning(lang.transl('_'));
        }
        const tips = [];
        !settings.downAllAges && tips.push(lang.transl('_'));
        !settings.downR18 && tips.push('R-18');
        !settings.downR18G && tips.push('R-18G');
        if (tips.length > 0) {
            log.warning(lang.transl('_') + tips.join(', '));
        }
    }
    getAIWorkType() {
        const tips = [];
        !settings.AIGenerated && tips.push(lang.transl('_AI'));
        !settings.notAIGenerated && tips.push(lang.transl('_AI'));
        !settings.UnknownAI && tips.push(lang.transl('_') + '(AI)');
        if (tips.length > 0) {
            log.warning(lang.transl('_') + tips.join(', '));
        }
    }
    getDownTypeByImgCount() {
        const tips = [];
        !settings.downSingleImg && tips.push(lang.transl('_'));
        !settings.downMultiImg && tips.push(lang.transl('_'));
        if (tips.length > 0) {
            log.warning(lang.transl('_') + tips.join(', '));
        }
    }
    // 
    getDownTypeByColor() {
        // 
        if (!settings.downColorImg && !settings.downBlackWhiteImg) {
            this.showWarning(lang.transl('_'));
        }
        const tips = [];
        !settings.downColorImg && tips.push(lang.transl('_'));
        !settings.downBlackWhiteImg && tips.push(lang.transl('_'));
        if (tips.length > 0) {
            log.warning(lang.transl('_') + tips.join(', '));
        }
    }
    // 
    getDownTypeByBmked() {
        // 
        if (!settings.downNotBookmarked && !settings.downBookmarked) {
            this.showWarning(lang.transl('_'));
        }
        const tips = [];
        !settings.downNotBookmarked && tips.push(lang.transl('_'));
        !settings.downBookmarked && tips.push(lang.transl('_'));
        if (tips.length > 0) {
            log.warning(lang.transl('_') + tips.join(', '));
        }
    }
    // 
    getMultiImageWorkImageLimit() {
        if (!settings.multiImageWorkImageLimitSwitch) {
            return;
        }
        if (settings.multiImageWorkImageLimit > 0) {
            log.warning(lang.transl('_') +
                '' +
                settings.multiImageWorkImageLimit);
        }
    }
    // tag
    getIncludeTag() {
        if (!settings.needTagSwitch) {
            return;
        }
        if (settings.needTag.length > 0) {
            log.warning(lang.transl('_tag') + settings.needTag.toString());
        }
    }
    // tag
    getExcludeTag() {
        if (!settings.notNeedTagSwitch) {
            return;
        }
        if (settings.notNeedTag.length > 0) {
            log.warning(lang.transl('_tag') + settings.notNeedTag.toString());
        }
    }
    // 
    getSetWh() {
        if (!settings.setWHSwitch) {
            return;
        }
        if (settings.setWidth || settings.setHeight) {
            const andOr = settings.setWidthAndOr
                .replace('|', lang.transl('_'))
                .replace('&', lang.transl('_'));
            const text = `${lang.transl('_')} ${settings.widthHeightLimit} ${settings.setWidth} ${andOr} ${lang.transl('_')} ${settings.widthHeightLimit} ${settings.setHeight}`;
            log.warning(text);
        }
    }
    // 
    getBMKNum() {
        if (!settings.BMKNumSwitch) {
            return;
        }
        const min = settings.BMKNumMin;
        const max = settings.BMKNumMax;
        const average = settings.BMKNumAverage;
        if (min >= 0) {
            log.warning(lang.transl('_') + min);
        }
        if (max >= 0) {
            log.warning(lang.transl('_') + max);
        }
        if (average >= 0 && settings.BMKNumAverageSwitch) {
            log.warning(`${lang.transl('_')} >= ${average}`);
        }
    }
    // 
    getRatio() {
        if (!settings.ratioSwitch) {
            return;
        }
        switch (settings.ratio) {
            case 'square':
                log.warning(lang.transl('_', lang.transl('_')));
                break;
            case 'horizontal':
                log.warning(lang.transl('_', lang.transl('_')));
                break;
            case 'vertical':
                log.warning(lang.transl('_', lang.transl('_')));
                break;
            case 'userSet':
                log.warning(lang.transl('_') +
                    ` ${settings.userRatioLimit} ` +
                    settings.userRatio);
                break;
        }
    }
    //  id 
    getIdRange() {
        if (!settings.idRangeSwitch) {
            return;
        }
        log.warning(`id ${settings.idRange} ${settings.idRangeInput}`);
    }
    // 
    getPostDate() {
        if (!settings.postDate) {
            return;
        }
        if (isNaN(settings.postDateStart) || isNaN(settings.postDateStart)) {
            const msg = lang.transl('_');
            this.showWarning(msg);
        }
        else {
            const start = new Date(settings.postDateStart).toLocaleString();
            const end = new Date(settings.postDateEnd).toLocaleString();
            log.warning(`${lang.transl('_')}: ${start} - ${end}`);
        }
    }
    // 
    getSize() {
        if (!settings.sizeSwitch) {
            return;
        }
        log.warning(`Size: ${settings.sizeMin}MiB - ${settings.sizeMax}MiB`);
    }
    getBlockList() {
        if (!settings.userBlockList) {
            return;
        }
        for (const uid of settings.blockList) {
            if (isNaN(Number.parseInt(uid))) {
                return this.showWarning(lang.transl('_ID'));
            }
        }
        if (settings.blockList.length > 0) {
            log.warning(lang.transl('_') + ': ' + settings.blockList.toString());
        }
    }
    // ---------------- check ----------------
    // 
    checkDownType(workType) {
        switch (workType) {
            case 0:
                return settings.downType0;
            case 1:
                return settings.downType1;
            case 2:
                return settings.downType2;
            case 3:
                return settings.downType3;
            default:
                return true;
        }
    }
    checkDownTypeByAge(xRestrict) {
        switch (xRestrict) {
            case 0:
                return settings.downAllAges;
            case 1:
                return settings.downR18;
            case 2:
                return settings.downR18G;
            default:
                return true;
        }
    }
    checkAIWorkType(aiType) {
        switch (aiType) {
            case 0:
                return settings.UnknownAI;
            case 1:
                return settings.notAIGenerated;
            case 2:
                return settings.AIGenerated;
            default:
                return true;
        }
    }
    // 
    checkMultiImageWorkImageLimit(workType, pageCount) {
        //  2 
        if (!settings.multiImageWorkImageLimitSwitch ||
            settings.multiImageWorkImageLimit < 1 ||
            pageCount === undefined ||
            pageCount < 2 ||
            (workType !== 0 && workType !== 1)) {
            return true;
        }
        return pageCount <= settings.multiImageWorkImageLimit;
    }
    // 
    checkPageCount(workType, pageCount) {
        if (workType === undefined || pageCount === undefined) {
            return true;
        }
        // 
        if (workType === 2) {
            pageCount = 1;
        }
        if (pageCount === 1) {
            return settings.downSingleImg;
        }
        if (pageCount > 1) {
            return settings.downMultiImg;
        }
        return false;
    }
    // 
    async checkBlackWhite(imgUrl) {
        // 
        if (!imgUrl || (settings.downColorImg && settings.downBlackWhiteImg)) {
            return true;
        }
        // result  truefalse 
        const result = await blackAndWhiteImage.check(imgUrl);
        return ((result && settings.downBlackWhiteImg) ||
            (!result && settings.downColorImg));
    }
    // 
    checkDownTypeByBmked(bookmarked) {
        // 
        if (bookmarked === undefined) {
            return true;
        }
        if (settings.downNotBookmarked && settings.downBookmarked) {
            return true;
        }
        if (!settings.downNotBookmarked && settings.downBookmarked) {
            // 
            return !!bookmarked;
        }
        else if (settings.downNotBookmarked && !settings.downBookmarked) {
            // 
            return !bookmarked;
        }
        return false;
    }
    checkBMK(bmk, date) {
        if (bmk === undefined || !settings.BMKNumSwitch) {
            return true;
        }
        // 
        const checkNumber = bmk >= settings.BMKNumMin && bmk <= settings.BMKNumMax;
        // 
        if (!settings.BMKNumAverageSwitch || date === undefined) {
            return checkNumber;
        }
        // 
        const createTime = new Date(date).getTime();
        const nowTime = new Date().getTime();
        //  4 
        if (nowTime - createTime < this.minimumTime) {
            //  4 
            //  4 
            return bmk >= settings.BMKNumAverage ? true : checkNumber;
        }
        const day = (nowTime - createTime) / this.oneDayTime; // 
        const average = bmk / day;
        const checkAverage = average >= settings.BMKNumAverage;
        // 
        return checkNumber || checkAverage;
    }
    //  tag 
    checkIncludeTag(tags) {
        if (!settings.needTagSwitch ||
            settings.needTag.length === 0 ||
            tags === undefined) {
            return true;
        }
        let result = false;
        //  tag 
        const needTags = settings.needTag.map((val) => {
            return val.toLowerCase();
        });
        //  tag
        if (needTags.length > 0) {
            //  tag 
            // Fate/grandorder  Fate/GrandOrder  tag Set  id 51811780
            const workTags = new Set();
            for (const tag of tags) {
                workTags.add(tag.toLowerCase());
            }
            // 
            if (settings.needTagMode === 'all') {
                let tagNeedMatched = 0;
                for (const tag of workTags) {
                    for (const need of needTags) {
                        if (tag === need) {
                            tagNeedMatched++;
                            break;
                        }
                    }
                }
                // 
                if (tagNeedMatched >= needTags.length) {
                    result = true;
                }
            }
            else {
                // 
                for (const tag of workTags.values()) {
                    if (needTags.includes(tag)) {
                        result = true;
                        break;
                    }
                }
            }
        }
        else {
            result = true;
        }
        return result;
    }
    //  tag , 
    checkExcludeTag(tags) {
        if (!settings.notNeedTagSwitch ||
            settings.notNeedTag.length === 0 ||
            tags === undefined) {
            return true;
        }
        const notNeedTags = settings.notNeedTag.map((str) => str.toLowerCase());
        for (const tag of tags) {
            for (const notNeed of notNeedTags) {
                // 
                if (settings.tagMatchMode === 'partial') {
                    if (tag.toLowerCase().includes(notNeed)) {
                        //  tag
                        //  tag 
                        // bl tag  Strike the Blood  Blue Poison 
                        // 
                        const words = tag.split(' ');
                        if (words.length > 1) {
                            //  tag  tag 
                            if (words.some((word) => word.toLowerCase() === notNeed)) {
                                return false;
                            }
                        }
                        else {
                            //  tag 
                            return false;
                        }
                    }
                }
                else {
                    // 
                    if (tag.toLowerCase() === notNeed) {
                        return false;
                    }
                }
            }
        }
        return true;
    }
    // 
    checkWidthHeight(width, height) {
        if (!settings.setWHSwitch ||
            width === undefined ||
            height === undefined ||
            width === 0 ||
            height === 0) {
            return true;
        }
        const setWidth = settings.setWidth;
        const setHeight = settings.setHeight;
        // 
        if (setWidth === 0 && setHeight === 0) {
            return true;
        }
        if (settings.widthHeightLimit === '>=') {
            // 
            if (settings.setWidthAndOr === '&') {
                return width >= setWidth && height >= setHeight;
            }
            else {
                return width >= setWidth || height >= setHeight;
            }
        }
        else if (settings.widthHeightLimit === '<=') {
            // 
            if (settings.setWidthAndOr === '&') {
                return width <= setWidth && height <= setHeight;
            }
            else {
                return width <= setWidth || height <= setHeight;
            }
        }
        else {
            // 
            if (settings.setWidthAndOr === '&') {
                return width === setWidth && height === setHeight;
            }
            else {
                return width === setWidth || height === setHeight;
            }
        }
    }
    // 
    checkRatio(width, height) {
        if (!settings.ratioSwitch ||
            width === undefined ||
            height === undefined ||
            width === 0 ||
            height === 0) {
            return true;
        }
        switch (settings.ratio) {
            case 'square':
                return width === height;
            case 'horizontal':
                return width / height > 1;
            case 'vertical':
                return width / height < 1;
            case 'userSet':
                switch (settings.userRatioLimit) {
                    case '>=':
                        return width / height >= settings.userRatio;
                    case '=':
                        return width / height === settings.userRatio;
                    case '<=':
                        return width / height <= settings.userRatio;
                }
        }
    }
    //  id 
    checkIdRange(id) {
        if (id === undefined || !settings.idRangeSwitch) {
            return true;
        }
        const setId = settings.idRangeInput;
        let nowId;
        if (typeof id !== 'number') {
            nowId = parseInt(id);
        }
        else {
            nowId = id;
        }
        if (settings.idRange === '>') {
            return nowId > setId;
        }
        else {
            return nowId < setId;
        }
    }
    // 
    checkPostDate(date) {
        if (!settings.postDate || date === undefined) {
            return true;
        }
        const time = new Date(date).getTime();
        return time >= settings.postDateStart && time <= settings.postDateEnd;
    }
    checkIdPublishTime(id, type) {
        if (id === undefined || !settings.postDate || !type) {
            return true;
        }
        const _id = Number.parseInt(id);
        const _type = type === 'novels' ? 'novels' : 'illusts';
        const range = workPublishTime.getTimeRange(_id, _type);
        // console.log(new Date(range[0]).toLocaleString())
        // console.log(new Date(range[1]).toLocaleString())
        // 
        // 
        if (range[0] > settings.postDateEnd || range[1] < settings.postDateStart) {
            return false;
        }
        // 
        if (range[1] - range[0] >= settings.postDateEnd - settings.postDateStart) {
            return true;
        }
        // 
        if (range[0] < settings.postDateStart &&
            range[1] > settings.postDateStart &&
            range[1] < settings.postDateEnd) {
            return true;
        }
        if (range[0] > settings.postDateStart &&
            range[0] < settings.postDateEnd &&
            range[1] > settings.postDateEnd) {
            return true;
        }
        // 
        return (range[0] >= settings.postDateStart && range[1] <= settings.postDateEnd);
    }
    // 
    // yes_rank  0
    checkDebut(yes_rank) {
        if (!states.debut || yes_rank === undefined) {
            return true;
        }
        return yes_rank === 0;
    }
    checkBlockList(userId) {
        if (!settings.userBlockList || userId === undefined) {
            return true;
        }
        //  id false 
        return !settings.blockList.includes(userId);
    }
    checkSize(size) {
        if (!settings.sizeSwitch || size === undefined) {
            return true;
        }
        return (size >= settings.sizeMin * this.MiB && size <= settings.sizeMax * this.MiB);
    }
    async checkMuteUser(userId) {
        if (userId === undefined) {
            return true;
        }
        return !(await mute.checkUser(userId));
    }
    async checkMuteTag(tags) {
        if (tags === undefined) {
            return true;
        }
        //  tag  mute 
        for (const tag of tags) {
            if (await mute.checkTag(tag)) {
                return false;
            }
        }
        return true;
        // return !(tags.some((mute.checkTag.bind(mute))))
    }
    checkBlockTagsForSpecificUser(userId, tags) {
        if (!settings.blockTagsForSpecificUser ||
            userId === undefined ||
            tags === undefined) {
            return true;
        }
        // 
        return !blockTagsForSpecificUser.check(userId, tags);
    }
    // 
    showWarning(msg) {
        EVT.fire('wrongSetting');
        msgBox.error(msg);
    }
    bindEvents() {
        window.addEventListener(EVT.list.crawlStart, () => {
            this.showTip();
        });
    }
}
const filter = new Filter();
export { filter };
