// 
import { EVT } from '../EVT';
import { Tools } from '../Tools';
import { store } from '../store/Store';
import { log } from '../Log';
import { lang } from '../Lang';
import { Colors } from '../Colors';
import { setSetting, settings } from '../setting/Settings';
import { Download } from '../download/Download';
import { progressBar } from './ProgressBar';
import { downloadStates } from './DownloadStates';
import { ShowDownloadStates } from './ShowDownloadStates';
import { ShowSkipCount } from './ShowSkipCount';
import { ShowConvertCount } from './ShowConvertCount';
import { BookmarkAfterDL } from './BookmarkAfterDL';
import { states } from '../store/States';
import { Config } from '../Config';
import { toast } from '../Toast';
import { Utils } from '../utils/Utils';
import { pageType } from '../PageType';
import { msgBox } from '../MsgBox';
import './CheckWarningMessage';
class DownloadControl {
    constructor() {
        this.wrapper = document.createElement('div');
        /** */
        this.resultBtns = {
            exportCSV: document.createElement('button'),
            exportJSON: document.createElement('button'),
            importJSON: document.createElement('button'),
        };
        this.thread = 5; // 
        //  5
        //  Config.downloadThreadMax 
        this.taskBatch = 0; // 
        this.taskList = {}; //  id  key
        this.errorIdList = []; //  id
        this.downloaded = 0; // 
        this.stop = false; // 
        this.pause = false; // 
        this.waitingTimer = undefined;
        this.msgFlag = 'uuidTip';
        this.createResultBtns();
        this.createDownloadArea();
        this.bindEvents();
        const statusTipWrap = this.wrapper.querySelector('.down_status');
        new ShowDownloadStates(statusTipWrap);
        const skipTipWrap = this.wrapper.querySelector('.skip_tip');
        new ShowSkipCount(skipTipWrap);
        const convertTipWrap = this.wrapper.querySelector('.convert_tip');
        new ShowConvertCount(convertTipWrap);
        //  p 
        if (Utils.isPixiv()) {
            const bmkAfterDLTipWrap = this.wrapper.querySelector('.bmkAfterDL_tip');
            new BookmarkAfterDL(bmkAfterDLTipWrap);
        }
    }
    bindEvents() {
        window.addEventListener(EVT.list.crawlStart, () => {
            this.hideResultBtns();
            this.hideDownloadArea();
            this.reset();
        });
        for (const ev of [
            EVT.list.crawlComplete,
            EVT.list.resultChange,
            EVT.list.resume,
        ]) {
            window.addEventListener(ev, (ev) => {
                // 
                this.pause = ev.type === 'resume';
                // 
                window.setTimeout(() => {
                    this.readyDownload();
                }, 0);
            });
        }
        window.addEventListener(EVT.list.skipDownload, (ev) => {
            //  downloadSuccess 
            const data = ev.detail.data;
            this.downloadOrSkipAFile(data);
        });
        window.addEventListener(EVT.list.downloadError, (ev) => {
            const id = ev.detail.data;
            this.downloadError(id);
        });
        window.addEventListener(EVT.list.requestPauseDownload, (ev) => {
            // 
            this.pauseDownload();
        });
        // 
        chrome.runtime.onMessage.addListener((msg) => {
            var _a;
            if (!this.taskBatch) {
                return;
            }
            // UUID 
            if ((_a = msg.data) === null || _a === void 0 ? void 0 : _a.uuid) {
                log.error(lang.transl('_uuid'));
                msgBox.once(this.msgFlag, lang.transl('_uuid'), 'error');
            }
            // 
            if (msg.msg === 'downloaded') {
                //  BLOBURL
                URL.revokeObjectURL(msg.data.url);
                // 
                EVT.fire('downloadSuccess', msg.data);
                this.downloadOrSkipAFile(msg.data);
            }
            else if (msg.msg === 'download_err') {
                // 
                // 
                // 
                //  IDM 
                if (msg.err === 'USER_CANCELED') {
                    log.error(lang.transl('_user_canceled_tip', Tools.createWorkLink(msg.data.id), msg.err || 'unknown'));
                    this.downloadOrSkipAFile(msg.data);
                    return;
                }
                // 
                log.error(lang.transl('_save_file_failed_tip', Tools.createWorkLink(msg.data.id), msg.err || 'unknown'));
                if (msg.err === 'FILE_FAILED') {
                    log.error(lang.transl('_FILE_FAILED_tip'));
                }
                EVT.fire('saveFileError');
                // 
                // 
                this.saveFileError(msg.data);
            }
        });
        window.addEventListener(EVT.list.downloadComplete, () => {
            // 
            if (store.waitingIdList.length === 0) {
                toast.success(lang.transl('_2'), {
                    position: 'center',
                });
                //  idList
                chrome.runtime.sendMessage({
                    msg: 'clearDownloadsTempData',
                });
            }
            else {
                window.clearTimeout(this.waitingTimer);
                this.waitingTimer = window.setTimeout(() => {
                    states.quickCrawl = true; // 
                    const idList = store.waitingIdList;
                    store.waitingIdList = [];
                    EVT.fire('crawlIdList', idList);
                }, 0);
            }
        });
    }
    createDownloadArea() {
        const html = `<div class="download_area">
    <div class="centerWrap_btns">
    <button class="startDownload" type="button" style="background:${Colors.bgBlue};" data-xztext="_"></button>
    <button class="pauseDownload" type="button" style="background:${Colors.bgYellow};" data-xztext="_"></button>
    <button class="stopDownload" type="button" style="background:${Colors.bgRed};" data-xztext="_"></button>
    <button class="copyUrl" type="button" style="background:${Colors.bgGreen};" data-xztext="_url"></button>
    </div>
    <div class="download_status_text_wrap">
    <span data-xztext="_"></span>
    <span class="down_status" data-xztext="_"></span>
    <span class="skip_tip warn"></span>
    <span class="convert_tip warn"></span>
    <span class="bmkAfterDL_tip green"></span>
    </div>
    </div>`;
        this.wrapper = Tools.useSlot('downloadArea', html);
        lang.register(this.wrapper);
        this.wrapper
            .querySelector('.startDownload')
            .addEventListener('click', () => {
            this.startDownload();
        });
        this.wrapper
            .querySelector('.pauseDownload')
            .addEventListener('click', () => {
            this.pauseDownload();
        });
        this.wrapper
            .querySelector('.stopDownload')
            .addEventListener('click', () => {
            this.stopDownload();
        });
        this.wrapper.querySelector('.copyUrl').addEventListener('click', () => {
            EVT.fire('showURLs');
        });
    }
    createResultBtns() {
        //  pixiv 
        if (Utils.isPixiv()) {
            // 
            this.resultBtns.importJSON = Tools.addBtn('exportResult', Colors.bgGreen, '_');
            // 
            // 
            this.resultBtns.importJSON.addEventListener('click', () => {
                EVT.fire('importResult');
            }, false);
            // 
            this.resultBtns.exportJSON = Tools.addBtn('exportResult', Colors.bgGreen, '_');
            this.resultBtns.exportJSON.style.display = 'none';
            this.resultBtns.exportJSON.addEventListener('click', () => {
                EVT.fire('exportResult');
            }, false);
            //  csv
            this.resultBtns.exportCSV = Tools.addBtn('exportResult', Colors.bgGreen, '_csv');
            this.resultBtns.exportCSV.style.display = 'none';
            this.resultBtns.exportCSV.addEventListener('click', () => {
                EVT.fire('exportCSV');
            }, false);
        }
    }
    // 
    readyDownload() {
        if (states.busy || states.mergeNovel) {
            return;
        }
        if (store.result.length === 0) {
            return progressBar.reset(0);
        }
        if (settings.downloadUgoiraFirst) {
            store.resultMeta.sort(Tools.sortUgoiraFirst);
            store.result.sort(Tools.sortUgoiraFirst);
        }
        EVT.fire('readyDownload');
        this.showResultBtns();
        this.showDownloadArea();
        this.setDownloaded();
        this.setDownloadThread();
        // 
        if (pageType.type === pageType.list.ArtworkSearch &&
            settings.previewResult) {
            // 
            // 
            // 
            if (!states.quickCrawl && !states.crawlTagList) {
                return;
            }
        }
        // 
        if (settings.autoStartDownload ||
            states.quickCrawl ||
            states.crawlTagList) {
            this.startDownload();
        }
    }
    // 
    startDownload() {
        if (states.busy) {
            return toast.error(lang.transl('_'));
        }
        if (store.result.length === 0) {
            return toast.error(lang.transl('_'));
        }
        if (this.pause) {
            // 
            // 
            downloadStates.resume();
        }
        else {
            // 
            // 
            downloadStates.init();
        }
        this.reset();
        msgBox.resetOnce(this.msgFlag);
        this.setDownloaded();
        this.taskBatch = new Date().getTime(); // 
        this.setDownloadThread();
        EVT.fire('downloadStart');
        // 
        for (let i = 0; i < this.thread; i++) {
            this.createDownload(i);
        }
        log.success(lang.transl('_'));
        if (Config.mobile) {
            log.warning(lang.transl('_Kiwibug'));
        }
    }
    // 
    pauseDownload() {
        if (store.result.length === 0) {
            return;
        }
        // 
        if (this.stop === true) {
            return;
        }
        if (this.pause === false) {
            // 
            if (states.busy) {
                this.pause = true;
                log.warning(lang.transl('_'), 2);
                EVT.fire('downloadPause');
            }
            else {
                // 
                return;
            }
        }
    }
    // 
    stopDownload() {
        if (store.result.length === 0 || this.stop) {
            return;
        }
        this.stop = true;
        log.error(lang.transl('_'), 2);
        this.pause = false;
        EVT.fire('downloadStop');
    }
    downloadError(id) {
        this.errorIdList.push(id);
        // 
        const task = this.taskList[id];
        const no = task.progressBarIndex;
        if (this.checkContinueDownload()) {
            this.createDownload(no);
        }
        else {
            this.checkCompleteWithError();
        }
    }
    setDownloaded() {
        this.downloaded = downloadStates.downloadedCount();
        const text = `${this.downloaded} / ${store.result.length}`;
        log.log(text, 2, false);
        // 
        progressBar.setTotalProgress(this.downloaded);
        store.remainingDownload = store.result.length - this.downloaded;
        // 
        if (this.downloaded === store.result.length) {
            log.success(lang.transl('_'), 2);
            window.setTimeout(() => {
                // 
                EVT.fire('downloadComplete');
            }, 0);
            this.reset();
        }
        this.checkCompleteWithError();
    }
    // 
    setDownloadThread() {
        const setThread = settings.downloadThread;
        if (setThread < 1 ||
            setThread > Config.downloadThreadMax ||
            isNaN(setThread)) {
            // 
            this.thread = Config.downloadThreadMax;
            setSetting('downloadThread', Config.downloadThreadMax);
        }
        else {
            this.thread = setThread; // 
        }
        // 
        if (store.result.length - this.downloaded < this.thread) {
            this.thread = store.result.length - this.downloaded;
        }
        // 
        progressBar.reset(this.thread, this.downloaded);
    }
    saveFileError(data) {
        if (this.pause || this.stop) {
            return false;
        }
        const task = this.taskList[data.id];
        // 
        downloadStates.setState(task.index, -1);
        // 
        this.createDownload(task.progressBarIndex);
    }
    downloadOrSkipAFile(data) {
        const task = this.taskList[data.id];
        // 
        downloadStates.setState(task.index, 1);
        // 
        this.setDownloaded();
        // 
        const no = task.progressBarIndex;
        if (this.checkContinueDownload()) {
            this.createDownload(no);
        }
    }
    // 
    checkContinueDownload() {
        // 
        if (this.downloaded < store.result.length) {
            // 
            if (this.pause || this.stop) {
                return false;
            }
            //   
            if (this.downloaded + this.thread - 1 < store.result.length) {
                return true;
            }
            else {
                return false;
            }
        }
        else {
            return false;
        }
    }
    // 
    createDownload(progressBarIndex) {
        const index = downloadStates.getFirstDownloadItem();
        if (index === undefined) {
            // 
            // 
            return this.checkCompleteWithError();
        }
        else {
            const workData = store.result[index];
            const argument = {
                id: workData.id,
                result: workData,
                index: index,
                progressBarIndex: progressBarIndex,
                taskBatch: this.taskBatch,
            };
            // 
            this.taskList[workData.id] = {
                index,
                progressBarIndex: progressBarIndex,
            };
            // 
            new Download(progressBarIndex, argument, index);
        }
    }
    // 
    checkCompleteWithError() {
        if (this.errorIdList.length > 0 &&
            this.downloaded + this.errorIdList.length === store.result.length) {
            // 
            this.pauseDownload();
            setTimeout(() => {
                this.startDownload();
            }, 2000);
        }
    }
    reset() {
        this.pause = false;
        this.stop = false;
        this.errorIdList = [];
        this.downloaded = 0;
    }
    showDownloadArea() {
        this.wrapper.style.display = 'block';
    }
    hideDownloadArea() {
        this.wrapper.style.display = 'none';
    }
    showResultBtns() {
        this.resultBtns.exportJSON.style.display = 'flex';
        this.resultBtns.exportCSV.style.display = 'flex';
    }
    hideResultBtns() {
        this.resultBtns.exportJSON.style.display = 'none';
        this.resultBtns.exportCSV.style.display = 'none';
    }
}
new DownloadControl();
