// 
import { lang } from '../Lang';
import { Colors } from '../Colors';
import { Tools } from '../Tools';
import { API } from '../API';
import { store } from '../store/Store';
import { log } from '../Log';
import { EVT } from '../EVT';
import { options } from '../setting/Options';
import { settings } from '../setting/Settings';
import { states } from '../store/States';
import { saveArtworkData } from '../store/SaveArtworkData';
import { saveNovelData } from '../store/SaveNovelData';
import { mute } from '../filter/Mute';
import '../SelectWork';
import { destroyManager } from '../pageFunciton/DestroyManager';
import { vipSearchOptimize } from './VipSearchOptimize';
import { toast } from '../Toast';
import { msgBox } from '../MsgBox';
import { Utils } from '../utils/Utils';
import { pageType } from '../PageType';
import { filter } from '../filter/Filter';
import { Config } from '../Config';
import { timedCrawl } from './TimedCrawl';
import '../pageFunciton/QuickBookmark';
import { setTimeoutWorker } from '../SetTimeoutWorker';
class InitPageBase {
    constructor() {
        this.crawlNumber = 0; // /
        this.maxCount = 1000; // /
        this.startpageNo = 1; //  api 
        this.listPageFinished = 0; // 
        this.ajaxThreadsDefault = 10; // 
        this.ajaxThread = this.ajaxThreadsDefault; // 
        this.finishedRequest = 0; //  id  ajaxThreads 
        /** */
        this.crawlStopped = false;
        this.log429ErrorTip = Utils.debounce(() => {
            log.error(lang.transl('_'));
        }, 500);
    }
    //  init 
    init() {
        this.setFormOption();
        this.addCrawlBtns();
        this.addAnyElement();
        this.initAny();
        //  init  destroy 
        //  destroy 
        destroyManager.register(this.destroy.bind(this));
        // 
        EVT.bindOnce('clearLogAfterPageSwitch', EVT.list.pageSwitch, () => {
            if (!states.busy) {
                log.remove();
            }
        });
        //  id 
        EVT.bindOnce('crawlIdList', EVT.list.crawlIdList, (ev) => {
            const idList = ev.detail.data;
            if (idList) {
                this.crawlIdList(idList);
            }
        });
    }
    // 
    setFormOption() {
        // /
        options.setWantPageTip({
            text: '_',
            tip: '_',
            rangTip: '_1',
        });
    }
    // 
    addCrawlBtns() {
        Tools.addBtn('crawlBtns', Colors.bgBlue, '_', '_').addEventListener('click', () => {
            this.readyCrawl();
        });
    }
    // 
    addAnyElement() { }
    // 
    //  init 
    // 
    initAny() { }
    // 
    destroy() {
        Tools.clearSlot('crawlBtns');
        Tools.clearSlot('otherBtns');
    }
    // /
    getWantPageError() {
        EVT.fire('wrongSetting');
        const msg = lang.transl('_');
        msgBox.error(msg);
        throw new Error(msg);
    }
    // /
    //  -1 0
    checkWantPageInput(crawlPartTip, crawlAllTip) {
        const want = settings.wantPageArr[pageType.type];
        //  1  -1
        if ((want < 1 && want !== -1) || isNaN(want)) {
            //  1  -1 , 0 
            throw this.getWantPageError();
        }
        if (want >= 1) {
            log.warning(crawlPartTip.replace('{}', want.toString()));
        }
        else if (want === -1) {
            log.warning(crawlAllTip);
        }
        return want;
    }
    // / 0
    //  max 
    //  page 
    checkWantPageInputGreater0(max, page) {
        const want = settings.wantPageArr[pageType.type];
        if (want > 0) {
            const result = want > max ? max : want;
            if (page) {
                log.warning(lang.transl('_x', result.toString()));
            }
            else {
                log.warning(lang.transl('_x', result.toString()));
            }
            return result;
        }
        else {
            throw this.getWantPageError();
        }
    }
    // 
    getWantPage() { }
    //  filter
    getMultipleSetting() {
        // 
        if (settings.firstFewImagesSwitch) {
            log.warning(`${lang.transl('_')} ${settings.firstFewImages}`);
        }
    }
    setSlowCrawl() {
        states.slowCrawlMode = settings.slowCrawl;
        if (settings.slowCrawl) {
            log.warning(lang.transl('_'));
        }
    }
    // 
    async readyCrawl() {
        // 
        if (states.busy) {
            toast.error(lang.transl('_'));
            return;
        }
        EVT.fire('clearLog');
        log.success(lang.transl('_'));
        toast.show(lang.transl('_'), {
            position: 'center',
        });
        EVT.fire('crawlStart');
        if (Utils.isPixiv()) {
            await mute.getMuteSettings();
        }
        this.getWantPage();
        this.getMultipleSetting();
        this.finishedRequest = 0;
        this.crawlStopped = false;
        // 
        this.nextStep();
    }
    //  id 
    //  id 
    //  id  getIdList 
    async crawlIdList(idList) {
        // 
        //  id 
        if (states.busy) {
            store.waitingIdList.push(...idList);
        }
        else {
            EVT.fire('clearLog');
            log.success(lang.transl('_'));
            toast.show(lang.transl('_'), {
                position: 'center',
            });
            EVT.fire('crawlStart');
            if (Utils.isPixiv()) {
                await mute.getMuteSettings();
            }
            this.getMultipleSetting();
            this.finishedRequest = 0;
            this.crawlStopped = false;
            store.idList = idList;
            this.getIdListFinished();
        }
    }
    // 
    nextStep() {
        this.getIdList();
    }
    //  id 
    getIdList() { }
    // id 
    getIdListFinished() {
        states.slowCrawlMode = false;
        this.resetGetIdListStatus();
        EVT.fire('getIdListFinished');
        if (states.bookmarkMode) {
            return;
        }
        if (store.idList.length === 0) {
            return this.noResult();
        }
        log.log(lang.transl('_', store.idList.length.toString()));
        //  return 
        // return states.busy = false
        log.log(lang.transl('_'));
        if (settings.slowCrawl &&
            store.idList.length > settings.slowCrawlOnWorksNumber) {
            // 
            log.warning(lang.transl('_'));
            states.slowCrawlMode = true;
            this.ajaxThread = 1;
        }
        else {
            // 
            states.slowCrawlMode = false;
            if (store.idList.length <= this.ajaxThreadsDefault) {
                this.ajaxThread = store.idList.length;
            }
            else {
                this.ajaxThread = this.ajaxThreadsDefault;
            }
        }
        for (let i = 0; i < this.ajaxThread; i++) {
            this.getWorksData();
        }
    }
    // 
    resetGetIdListStatus() { }
    // 
    async getWorksData(idData) {
        idData = idData || store.idList.shift();
        const id = idData.id;
        if (!id) {
            const msg = 'Error: work id is invalid!';
            msgBox.error(msg);
            throw new Error(msg);
        }
        //  id 
        //  2 
        // 1.  id  id 
        // 2.  id 
        const check = await filter.check({
            id,
            workTypeString: idData.type,
        });
        if (!check) {
            return this.afterGetWorksData();
        }
        try {
            const unlisted = pageType.type === pageType.list.Unlisted;
            //  cacheWorkData
            if (idData.type === 'novels') {
                const data = await API.getNovelData(id, unlisted);
                await saveNovelData.save(data);
                this.afterGetWorksData(data);
            }
            else {
                const data = await API.getArtworkData(id, unlisted);
                await saveArtworkData.save(data);
                this.afterGetWorksData(data);
            }
        }
        catch (error) {
            //  API  reject
            // error: {
            //   status: response.status,
            //   statusText: response.statusText,
            // }
            if (error.status) {
                // 
                this.logErrorStatus(error.status, idData);
                if (error.status === 500 || error.status === 429) {
                    //  500  429 pixiv 
                    this.log429ErrorTip();
                    return window.setTimeout(() => {
                        this.getWorksData(idData);
                    }, Config.retryTime);
                }
                else {
                    this.afterGetWorksData();
                }
            }
            else {
                // 
                // TypeError: Failed to fetch
                //  save 
                console.error(error);
                // 
                window.setTimeout(() => {
                    this.getWorksData(idData);
                }, 2000);
            }
        }
    }
    // 
    async afterGetWorksData(data) {
        // 
        if (this.crawlStopped) {
            return;
        }
        this.logResultNumber();
        // 
        if (data && (await vipSearchOptimize.stopCrawl(data))) {
            // 
            this.crawlStopped = true;
            this.crawlFinished();
        }
        if (store.idList.length > 0) {
            // 
            if (states.slowCrawlMode) {
                setTimeoutWorker.set(() => {
                    this.getWorksData();
                }, Config.slowCrawlDealy);
            }
            else {
                this.getWorksData();
            }
        }
        else {
            // 
            this.finishedRequest++;
            // 
            if (this.finishedRequest === this.ajaxThread) {
                this.crawlFinished();
            }
        }
    }
    // 
    crawlFinished() {
        if (store.result.length === 0) {
            return this.noResult();
        }
        store.crawlCompleteTime = new Date();
        // 
        if (settings.setFileDownloadOrder) {
            // 
            if (settings.downloadOrderSortBy === 'ID') {
                store.result.sort(Utils.sortByProperty('id', settings.downloadOrder));
            }
            else if (settings.downloadOrderSortBy === 'bookmarkCount') {
                store.result.sort(Utils.sortByProperty('bmk', settings.downloadOrder));
            }
            else if (settings.downloadOrderSortBy === 'bookmarkID') {
                store.result.sort(Utils.sortByProperty('bmkId', settings.downloadOrder));
            }
        }
        else {
            // 
            this.sortResult();
        }
        log.log(lang.transl('_n', store.resultMeta.length.toString()));
        log.log(lang.transl('_n', store.result.length.toString()));
        log.success(lang.transl('_'), 2);
        // 
        EVT.fire('crawlComplete');
        // 
        if (settings.autoExportResult &&
            store.result.length > settings.autoExportResultNumber) {
            if (settings.autoExportResultCSV) {
                EVT.fire('exportCSV');
            }
            if (settings.autoExportResultJSON) {
                EVT.fire('exportResult');
            }
        }
    }
    // 
    logErrorStatus(status, idData) {
        const isNovel = idData.type === 'novels';
        const workLink = Tools.createWorkLink(idData.id, !isNovel);
        switch (status) {
            case 0:
                log.error(workLink + ' ' + lang.transl('_0'));
                break;
            case 400:
                log.error(workLink + ' ' + lang.transl('_400'));
                break;
            case 401:
                log.error(workLink + ' ' + lang.transl('_401'));
                break;
            case 403:
                log.error(workLink + ' ' + lang.transl('_403'));
                break;
            case 404:
                log.error(workLink + ' ' + lang.transl('_404'));
                break;
            case 429:
                log.error(workLink + ' ' + lang.transl('_429'));
                break;
            case 500:
                log.error(workLink + ' ' + lang.transl('_500'));
                break;
            default:
                log.error(lang.transl('_', workLink) + `HTTP status code: ${status}`);
                break;
        }
    }
    // 
    logResultNumber() {
        log.log(`${lang.transl('_')} ${store.idList.length}, ${lang.transl('_n', store.resultMeta.length.toString())}`, 1, false);
    }
    //  0 
    noResult() {
        //  crawlComplete crawlEmpty crawlEmpty 
        //  crawlComplete crawlEmpty 
        EVT.fire('crawlComplete');
        EVT.fire('crawlEmpty');
        const msg = lang.transl('_');
        log.error(msg, 2);
        msgBox.error(msg);
    }
    // 
    sortResult() { }
    /** */
    addStartTimedCrawlBtn(cb) {
        Tools.addBtn('crawlBtns', Colors.bgBlue, '_', '_').addEventListener('click', () => {
            timedCrawl.start(cb);
        });
    }
    /** */
    addCancelTimedCrawlBtn() {
        const btn = Tools.addBtn('crawlBtns', Colors.bgWarning, '_');
        btn.style.display = 'none';
        btn.addEventListener('click', () => {
            EVT.fire('cancelTimedCrawl');
            btn.style.display = 'none';
        });
        // 
        window.addEventListener(EVT.list.startTimedCrawl, () => {
            btn.style.display = 'flex';
        });
    }
}
export { InitPageBase };
