import { store } from '../store/Store';
import { settings } from '../setting/Settings';
import { lang } from '../Lang';
import { EVT } from '../EVT';
import { bookmark } from '../Bookmark';
import { log } from '../Log';
// 
class BookmarkAfterDL {
    constructor(tipEl) {
        //  id
        // 
        this.savedIds = [];
        this.successCount = 0;
        this.tipEl = document.createElement('span');
        // 
        this.delayReset = false;
        if (tipEl) {
            this.tipEl = tipEl;
            lang.register(this.tipEl);
        }
        this.bindEvents();
    }
    // 
    bindEvents() {
        //  id 
        window.addEventListener(EVT.list.downloadSuccess, (ev) => {
            const successData = ev.detail.data;
            this.send(Number.parseInt(successData.id));
        });
        // 
        // 
        // 
        window.addEventListener(EVT.list.skipDownload, (ev) => {
            const skipData = ev.detail.data;
            if (skipData.reason === 'duplicate') {
                this.send(Number.parseInt(skipData.id));
            }
        });
        // 
        window.addEventListener(EVT.list.crawlStart, (ev) => {
            this.reset();
        });
        window.addEventListener(EVT.list.downloadComplete, () => {
            this.delayReset = true;
        });
        window.addEventListener(EVT.list.downloadStart, () => {
            if (this.delayReset) {
                this.reset();
                this.delayReset = false;
            }
        });
    }
    showProgress() {
        if (this.savedIds.length === 0) {
            lang.updateText(this.tipEl, '');
            return;
        }
        lang.updateText(this.tipEl, '_', `${this.successCount}/${this.savedIds.length}`);
        if (this.successCount === this.savedIds.length) {
            log.success(lang.transl('_'));
        }
    }
    reset() {
        this.savedIds = [];
        this.successCount = 0;
        this.tipEl.classList.remove('red');
        this.tipEl.classList.add('green');
        this.showProgress();
    }
    //  id
    send(id) {
        if (!settings.bmkAfterDL) {
            return;
        }
        if (typeof id !== 'number') {
            id = Number.parseInt(id);
        }
        //  id 
        if (this.savedIds.includes(id)) {
            return;
        }
        this.addBookmark(id);
    }
    //  tag  tag
    async addBookmark(id) {
        return new Promise(async (resolve, reject) => {
            this.savedIds.push(id);
            this.showProgress();
            //  store 
            const dataSource = store.resultMeta.length > 0 ? store.resultMeta : store.result;
            const data = dataSource.find((val) => val.idNum === id);
            if (data === undefined) {
                return reject(new Error(`Not find ${id} in result`));
            }
            const res = await bookmark.add(id.toString(), data.type !== 3 ? 'illusts' : 'novels', data.tags, undefined, undefined, true);
            this.successCount++;
            this.showProgress();
            resolve();
        });
    }
}
export { BookmarkAfterDL };
