import { lang } from '../Lang';
import { API } from '../API';
import { log } from '../Log';
import { Tools } from '../Tools';
import { EVT } from '../EVT';
import { Utils } from '../utils/Utils';
import { toast } from '../Toast';
// 
class SaveAvatarImage {
    constructor() {
        this.bindEvents();
    }
    bindEvents() {
        window.addEventListener(EVT.list.saveAvatarImage, () => {
            this.saveAvatarImage();
        });
    }
    async saveAvatarImage() {
        const userId = Tools.getUserId();
        const userProfile = await API.getUserProfile(userId);
        const imageURL = userProfile.body.imageBig;
        // 
        const arr = imageURL.split('.');
        const ext = arr[arr.length - 1];
        // imageBig  170 px 
        //  gif  url gif  url 
        //  170 
        const fullSizeImgURL = ext === 'gif' ? imageURL : imageURL.replace('_170', '');
        // 
        const img = await fetch(fullSizeImgURL);
        const blob = await img.blob();
        // 
        const url = URL.createObjectURL(blob);
        const name = `${userProfile.body.name}_${userId}_avatar.${ext}`;
        Utils.downloadFile(url, name);
        const msg = ' ' + lang.transl('_');
        log.success(msg);
        toast.success(msg);
        EVT.fire('closeCenterPanel');
    }
}
new SaveAvatarImage();
