import { EVT } from '../EVT';
import { Tools } from '../Tools';
import { lang } from '../Lang';
import { Config } from '../Config';
import { store } from '../store/Store';
import { fileName } from '../FileName';
import { createCSV } from '../utils/CreateCSV';
import { toast } from '../Toast';
import { Utils } from '../utils/Utils';
import { showHelp } from '../ShowHelp';
// name  csv 
// index 
//  csv 
class ExportResult2CSV {
    constructor() {
        this.xRestrictMap = new Map([
            [0, 'AllAges'],
            [1, 'R-18'],
            [2, 'R-18G'],
        ]);
        this.AIType = ['Unknown', 'No', 'Yes'];
        // 
        this.fieldCfg = [
            {
                name: 'id',
                index: 'idNum',
            },
            {
                name: 'tags',
                index: 'tags',
            },
            {
                name: 'tags_transl',
                index: 'tagsTranslOnly',
            },
            {
                name: 'user',
                index: 'user',
            },
            {
                name: 'userId',
                index: 'userId',
            },
            {
                name: 'title',
                index: 'title',
            },
            {
                name: 'description',
                index: 'description',
            },
            {
                name: 'type',
                index: 'type',
            },
            {
                name: 'page',
                index: 'pageCount',
            },
            {
                name: 'bookmark',
                index: 'bmk',
            },
            {
                name: 'bookmarked',
                index: 'bookmarked',
            },
            {
                name: 'likeCount',
                index: 'likeCount',
            },
            {
                name: 'viewCount',
                index: 'viewCount',
            },
            {
                name: 'commentCount',
                index: 'commentCount',
            },
            {
                name: 'width',
                index: 'fullWidth',
            },
            {
                name: 'height',
                index: 'fullHeight',
            },
            {
                name: 'xRestrict',
                index: 'xRestrict',
            },
            {
                name: 'AI',
                index: 'aiType',
            },
            {
                name: 'date',
                index: 'date',
            },
            {
                name: 'original',
                index: 'original',
            },
            {
                name: 'thumb',
                index: 'thumb',
            },
            // fileName  index 
            {
                name: 'fileName',
                index: 'title',
            },
        ];
        window.addEventListener(EVT.list.exportCSV, () => {
            this.beforeCreate();
        });
    }
    beforeCreate() {
        // 
        if (store.result.length === 0) {
            toast.error(lang.transl('_'));
            return;
        }
        //  result  resultMeta result resultMeta result 
        // 1 result resultMeta 
        // 2:  A resultMeta  A  B B  resultMeta  A 
        //  result 
        this.create(store.result);
    }
    create(data) {
        var _a, _b;
        const body = []; // 
        // 
        const head = [];
        for (const field of this.fieldCfg) {
            head.push(field.name);
        }
        body.push(head);
        // 
        for (const d of data) {
            // 
            // 
            const index = (_a = d.index) !== null && _a !== void 0 ? _a : Tools.getResultIndex(d);
            if (index > 0) {
                continue;
            }
            const bodyItem = []; // 
            // 
            for (const field of this.fieldCfg) {
                if (field.name === 'fileName') {
                    bodyItem.push(fileName.createFileName(d));
                }
                else {
                    let result = (_b = d[field.index]) !== null && _b !== void 0 ? _b : '';
                    // 
                    if (field.name === 'type') {
                        result = Config.worksTypeName[result];
                    }
                    if (field.name === 'bookmarked') {
                        result = result ? 'Yes' : 'No';
                    }
                    if (field.name === 'xRestrict') {
                        result = this.xRestrictMap.get(result) || '';
                    }
                    if (field.name === 'AI') {
                        result = this.AIType[d.aiType || 0];
                    }
                    bodyItem.push(result);
                }
            }
            // 
            body.push(bodyItem);
        }
        const csv = createCSV.create(body);
        const csvURL = URL.createObjectURL(csv);
        // 
        let csvName = `result-${Utils.replaceUnsafeStr(Tools.getPageTitle())}-${store.crawlCompleteTime.getTime()}.csv`;
        Utils.downloadFile(csvURL, csvName);
        toast.success(lang.transl('_'));
        showHelp.show('tipCSV', lang.transl('_CSV'));
    }
}
new ExportResult2CSV();
