// artwork 
import { InitPageBase } from '../crawl/InitPageBase';
import { Colors } from '../Colors';
import { EVT } from '../EVT';
import { lang } from '../Lang';
import { options } from '../setting/Options';
import { store } from '../store/Store';
import { ImageViewer } from '../ImageViewer';
import { Tools } from '../Tools';
import { API } from '../API';
import { log } from '../Log';
import { Utils } from '../utils/Utils';
class InitArtworkPage extends InitPageBase {
    constructor() {
        super();
        this.crawlDirection = 0; // 
        /*
        -1 
        0 
        1 
        */
        this.crawlRelated = false; // 
        this.init();
    }
    initAny() {
        this.initImgViewer();
        window.addEventListener(EVT.list.pageSwitchedTypeNotChange, this.initImgViewer);
    }
    initImgViewer() {
        new ImageViewer({
            showImageList: true,
            imageListId: 'viewerWarpper',
            insertTarget: 'main figcaption',
            insertPostion: 'beforebegin',
        });
    }
    addCrawlBtns() {
        Tools.addBtn('crawlBtns', Colors.bgBlue, '_new').addEventListener('click', () => {
            this.crawlDirection = -1;
            this.readyCrawl();
        });
        Tools.addBtn('crawlBtns', Colors.bgBlue, '_old').addEventListener('click', () => {
            this.crawlDirection = 1;
            this.readyCrawl();
        });
        const downRelatedBtn = Tools.addBtn('crawlBtns', Colors.bgBlue, '_');
        downRelatedBtn.addEventListener('click', () => {
            this.crawlRelated = true;
            this.readyCrawl();
        }, false);
    }
    setFormOption() {
        // /
        options.setWantPageTip({
            text: '_',
            tip: '_',
            rangTip: '_1',
        });
    }
    destroy() {
        Tools.clearSlot('crawlBtns');
        Tools.clearSlot('otherBtns');
        window.removeEventListener(EVT.list.pageSwitchedTypeNotChange, this.initImgViewer);
    }
    getWantPage() {
        // 
        if (!this.crawlRelated) {
            const crawlAllTip = this.crawlDirection === -1
                ? lang.transl('_new')
                : lang.transl('_old');
            this.crawlNumber = this.checkWantPageInput(lang.transl('_x'), crawlAllTip);
        }
        else {
            // 
            this.crawlNumber = this.checkWantPageInput(lang.transl('_x'), lang.transl('_'));
        }
    }
    nextStep() {
        // 
        if (this.crawlRelated) {
            this.getRelatedList();
        }
        else {
            // 
            this.getIdList();
        }
    }
    async getIdList() {
        let type = ['illusts', 'manga'];
        let idList = await API.getUserWorksByType(Tools.getUserId(), type);
        //  id
        let nowId = parseInt(Tools.getIllustId(window.location.href));
        idList.forEach((id) => {
            let idNum = parseInt(id.id);
            // 
            if (idNum >= nowId && this.crawlDirection === -1) {
                store.idList.push(id);
            }
            else if (idNum <= nowId && this.crawlDirection === 1) {
                // 
                store.idList.push(id);
            }
        });
        // 
        if (this.crawlNumber !== -1) {
            //  
            if (this.crawlDirection === -1) {
                store.idList.sort(Utils.sortByProperty('id')).reverse();
            }
            else {
                //  
                store.idList.sort(Utils.sortByProperty('id'));
            }
            store.idList = store.idList.splice(0, this.crawlNumber);
        }
        this.getIdListFinished();
    }
    // 
    async getRelatedList() {
        let data = await API.getRelatedData(Tools.getIllustId());
        // 
        let ids = [];
        for (const illust of data.body.illusts) {
            if (illust.isAdContainer) {
                continue;
            }
            ids.push(illust.id);
        }
        ids = ids.concat(data.body.nextIds);
        // 
        if (this.crawlNumber !== -1) {
            ids = ids.slice(0, this.crawlNumber);
        }
        for (const id of ids) {
            store.idList.push({
                type: 'unknown',
                id,
            });
        }
        log.log(lang.transl('_', store.idList.length.toString()));
        this.getIdListFinished();
    }
    resetGetIdListStatus() {
        this.crawlDirection = 0; // 
        this.crawlRelated = false; // 
    }
}
export { InitArtworkPage };
