// 
import { InitPageBase } from '../crawl/InitPageBase';
import { API } from '../API';
import { Colors } from '../Colors';
import { lang } from '../Lang';
import { options } from '../setting/Options';
import { store } from '../store/Store';
import { log } from '../Log';
import { Tools } from '../Tools';
import { token } from '../Token';
import { BookmarksAddTag } from '../pageFunciton/BookmarksAddTag';
import { filter } from '../filter/Filter';
import { Utils } from '../utils/Utils';
import { Config } from '../Config';
import { states } from '../store/States';
import { setTimeoutWorker } from '../SetTimeoutWorker';
import { toast } from '../Toast';
import { unBookmarkWorks } from '../UnBookmarkWorks';
import { removeWorksTagsInBookmarks } from '../RemoveWorksTagsInBookmarks';
import { EVT } from '../EVT';
class InitBookmarkPage extends InitPageBase {
    constructor() {
        super();
        this.idList = []; //  id
        this.type = 'illusts'; // 
        this.isHide = false; // 
        this.requsetNumber = 0; // 
        this.filteredNumber = 0; // 
        this.onceRequest = 100; // 
        this.offset = 0; // 
        // 
        this.crawlMode = 'normal';
        this.init();
    }
    addCrawlBtns() {
        Tools.addBtn('crawlBtns', Colors.bgBlue, '_', '_').addEventListener('click', () => {
            this.readyCrawl();
        });
        this.addStartTimedCrawlBtn(this.readyCrawl.bind(this));
        this.addCancelTimedCrawlBtn();
    }
    setFormOption() {
        // /
        options.setWantPageTip({
            text: '_',
            tip: '_',
            rangTip: '_1',
        });
    }
    getWantPage() {
        this.crawlNumber = this.checkWantPageInput(lang.transl('_x'), lang.transl('_'));
    }
    addAnyElement() {
        //  token
        if (!token.token) {
            return;
        }
        // 
        let ownPage = false;
        const URLUserID = Utils.getURLPathField(window.location.pathname, 'users');
        if (!URLUserID) {
            ownPage = true;
        }
        else {
            //  userID URL  userID 
            const element = document.querySelector('#qualtrics_user-id');
            if (!element || !element.textContent) {
                ownPage = false;
            }
            else {
                ownPage =
                    element.textContent ===
                        Utils.getURLPathField(window.location.pathname, 'users');
            }
            //  pixiv 
            //  head  script  userID URL  userID 
            // 'user_id', "1234567"
            if (!ownPage) {
                ownPage = document.head.innerHTML.includes(`'user_id', "${URLUserID}"`);
            }
        }
        if (!ownPage) {
            return;
        }
        const btn = Tools.addBtn('otherBtns', Colors.bgGreen, '_tag');
        new BookmarksAddTag(btn);
        Tools.addBtn('otherBtns', Colors.bgGreen, '_').addEventListener('click', () => {
            this.removeWorksTagsOnThisPage();
        });
        Tools.addBtn('otherBtns', Colors.bgGreen, '_').addEventListener('click', () => {
            this.unBookmarkAllWorksOnThisPage();
        });
    }
    // 
    unBookmarkAllWorksOnThisPage() {
        if (states.busy ||
            this.crawlMode === 'removeTags' ||
            this.crawlMode === 'unBookmark') {
            toast.error(lang.transl('_'));
            return;
        }
        // 
        this.crawlMode = 'unBookmark';
        log.warning(lang.transl('_'));
        toast.warning(lang.transl('_'), {
            position: 'topCenter',
        });
        EVT.fire('closeCenterPanel');
        //  1
        this.crawlNumber = 1;
        store.tag = Tools.getTagFromURL();
        this.readyGetIdList();
        this.getIdList();
    }
    // 
    removeWorksTagsOnThisPage() {
        if (states.busy ||
            this.crawlMode === 'removeTags' ||
            this.crawlMode === 'unBookmark') {
            toast.error(lang.transl('_'));
            return;
        }
        // 
        this.crawlMode = 'removeTags';
        log.warning(lang.transl('_'));
        log.warning(lang.transl('_'));
        toast.warning(lang.transl('_'), {
            position: 'topCenter',
        });
        EVT.fire('closeCenterPanel');
        //  1
        this.crawlNumber = 1;
        store.tag = Tools.getTagFromURL();
        this.readyGetIdList();
        this.getIdList();
    }
    nextStep() {
        this.crawlMode = 'normal';
        this.setSlowCrawl();
        this.readyGetIdList();
        this.getIdList();
    }
    readyGetIdList() {
        if (window.location.pathname.includes('/novel')) {
            this.type = 'novels';
        }
        //  48  24 
        const onceNumber = window.location.pathname.includes('/novels') ? 24 : 48;
        // 
        const nowPage = Utils.getURLSearchField(location.href, 'p'); //  1 
        if (nowPage) {
            this.offset = (parseInt(nowPage) - 1) * onceNumber;
        }
        if (this.offset < 0) {
            this.offset = 0;
        }
        // 
        if (this.crawlNumber === -1) {
            this.requsetNumber = Config.worksNumberLimit;
        }
        else {
            this.requsetNumber = onceNumber * this.crawlNumber;
        }
        // 
        //  url rest 
        this.isHide = Utils.getURLSearchField(location.href, 'rest') === 'hide';
        log.log(lang.transl('_'));
        if (this.crawlNumber === -1) {
            log.log(lang.transl('_'));
        }
    }
    // 
    async getIdList() {
        let data;
        try {
            data = await API.getBookmarkData(Tools.getUserId(), this.type, store.tag, this.offset, this.isHide);
        }
        catch (error) {
            this.getIdList();
            return;
        }
        if (data.body.works.length === 0 ||
            this.idList.length >= this.requsetNumber ||
            this.filteredNumber >= this.requsetNumber) {
            // 
            return this.afterGetIdList();
        }
        else {
            // 
            const idType = this.type === 'illusts' ? 'unknown' : 'novels';
            for (const workData of data.body.works) {
                if (this.filteredNumber >= this.requsetNumber) {
                    return this.afterGetIdList();
                }
                const filterOpt = {
                    aiType: workData.aiType,
                    id: workData.id,
                    tags: workData.tags,
                    bookmarkData: workData.bookmarkData,
                    createDate: workData.createDate,
                    userId: workData.userId,
                    xRestrict: workData.xRestrict,
                };
                this.filteredNumber++;
                if (await filter.check(filterOpt)) {
                    this.idList.push({
                        type: idType,
                        id: workData.id,
                    });
                }
            }
            this.offset += this.onceRequest; // 
            log.log(lang.transl('_', this.idList.length.toString()), 1, false);
            // 
            if (states.slowCrawlMode) {
                setTimeoutWorker.set(() => {
                    this.getIdList();
                }, Config.slowCrawlDealy);
            }
            else {
                this.getIdList();
            }
        }
    }
    //  id 
    afterGetIdList() {
        //  100 
        if (this.idList.length > this.requsetNumber) {
            // 
            this.idList.splice(this.requsetNumber, this.idList.length);
            //  api  id 
        }
        if (this.crawlMode === 'normal') {
            // 
            store.idList = store.idList.concat(this.idList);
            this.getIdListFinished();
        }
        else if (this.crawlMode === 'unBookmark') {
            // 
            //  id 
            const idList = Array.from(this.idList);
            this.resetGetIdListStatus();
            unBookmarkWorks.start(idList);
        }
        else if (this.crawlMode === 'removeTags') {
            // 
            //  id 
            const idList = Array.from(this.idList);
            this.resetGetIdListStatus();
            removeWorksTagsInBookmarks.start(idList);
        }
    }
    resetGetIdListStatus() {
        this.type = 'illusts';
        this.crawlMode = 'normal';
        this.idList = [];
        this.offset = 0;
        this.requsetNumber = 0;
        this.filteredNumber = 0;
    }
}
export { InitBookmarkPage };
