import { lang } from '../Lang';
import { API } from '../API';
import { log } from '../Log';
import { Tools } from '../Tools';
import { EVT } from '../EVT';
import { img2ico } from '../utils/imageToIcon';
import { Utils } from '../utils/Utils';
import { toast } from '../Toast';
// 
class SaveAvatarIcon {
    constructor() {
        this.bindEvents();
    }
    bindEvents() {
        window.addEventListener(EVT.list.saveAvatarIcon, () => {
            this.saveAvatarIcon();
        });
    }
    async saveAvatarIcon() {
        const userId = Tools.getUserId();
        const userProfile = await API.getUserProfile(userId);
        const bigImg = userProfile.body.imageBig; // imageBig  170 px 
        const fullSizeImg = bigImg.replace('_170', ''); //  170 
        //  ico 
        //  256 128 windows  48 
        const blob = await img2ico.convert({
            size: [256],
            source: fullSizeImg,
            shape: 'fillet',
            bleed: true,
        });
        // 
        const url = URL.createObjectURL(blob);
        const name = `${userProfile.body.name}_${userId}_icon.ico`;
        Utils.downloadFile(url, name);
        const msg = ' ' + lang.transl('_');
        log.success(msg);
        toast.success(msg);
        EVT.fire('closeCenterPanel');
    }
}
new SaveAvatarIcon();
