import { EVT } from './EVT';
import { theme } from './Theme';
import { Colors } from './Colors';
import { lang } from './Lang';
import { store } from './store/Store';
import { toast } from './Toast';
import { Tools } from './Tools';
import { Utils } from './utils/Utils';
import { settings } from './setting/Settings';
import { DateFormat } from './utils/DateFormat';
import { Config } from './Config';
// 
class Log {
    constructor() {
        this.id = 'logWrap'; //  id
        this.wrap = document.createElement('div'); // 
        this.logArea = document.createElement('div'); // 
        this.refresh = document.createElement('span'); // 
        this.levelColor = [
            'inherit',
            Colors.textSuccess,
            Colors.textWarning,
            Colors.textError,
        ];
        this.max = 300;
        this.count = 0;
        this.record = [];
        this.toBottom = false; //  true false
        this.scrollToBottom();
        window.addEventListener(EVT.list.clearLog, () => {
            this.clear();
        });
        const clearRecordEvents = [EVT.list.clearLog, EVT.list.downloadStop];
        clearRecordEvents.forEach((evt) => {
            window.addEventListener(evt, () => {
                this.record = [];
            });
        });
        window.addEventListener(EVT.list.crawlComplete, () => {
            if (settings.exportLog && settings.exportLogTiming === 'crawlComplete') {
                this.export();
            }
        });
        window.addEventListener(EVT.list.downloadComplete, () => {
            if (settings.exportLog &&
                settings.exportLogTiming === 'downloadComplete') {
                this.export();
            }
        });
    }
    // 
    /*
    str 
    level 
    br 
    keepShow  truefalse 
  
    level 
    0 normal
    1 success
    2 warning
    3 error
    */
    add(str, level, br, keepShow) {
        this.checkElement();
        let span = document.createElement('span');
        if (!keepShow) {
            span = this.refresh;
        }
        else {
            this.count++;
        }
        span.innerHTML = str;
        span.style.color = this.levelColor[level];
        while (br > 0) {
            span.appendChild(document.createElement('br'));
            br--;
        }
        this.logArea.appendChild(span);
        this.toBottom = true; // 
        // 
        if (keepShow) {
            this.record.push({ html: span.outerHTML, level });
        }
    }
    log(str, br = 1, keepShow = true) {
        this.add(str, 0, br, keepShow);
    }
    success(str, br = 1, keepShow = true) {
        this.add(str, 1, br, keepShow);
    }
    warning(str, br = 1, keepShow = true) {
        this.add(str, 2, br, keepShow);
    }
    error(str, br = 1, keepShow = true) {
        this.add(str, 3, br, keepShow);
    }
    checkElement() {
        // 
        let test = document.getElementById(this.id);
        if (test === null) {
            this.wrap = document.createElement('div');
            this.wrap.id = this.id;
            this.logArea = document.createElement('div');
            this.logArea.classList.add('beautify_scrollbar', 'logContent');
            if (Config.mobile) {
                this.wrap.classList.add('mobile');
            }
            this.wrap.append(this.logArea);
            document.body.insertAdjacentElement('beforebegin', this.wrap);
            theme.register(this.wrap);
            // 
            // bg.useBG(this.wrap, 0.9)
        }
        // 
        //  8000  1 GB 
        if (this.count > this.max) {
            this.clear();
        }
    }
    /** */
    remove() {
        this.count = 0;
        this.wrap.remove();
    }
    /** */
    clear() {
        this.count = 0;
        this.logArea.innerHTML = '';
    }
    // 
    scrollToBottom() {
        window.setInterval(() => {
            if (this.toBottom) {
                this.logArea.scrollTop = this.logArea.scrollHeight;
                this.toBottom = false;
            }
        }, 800);
    }
    export() {
        const data = [];
        for (const record of this.record) {
            let html = '';
            if (record.level !== 3 && settings.exportLogNormal) {
                html = record.html;
            }
            if (record.level === 3 && settings.exportLogError) {
                html = record.html;
            }
            // 
            if (html && settings.exportLogExclude.length > 0) {
                let checkStr = html;
                //  id 
                if (html.includes('<a href')) {
                    const array = html.split('</a>');
                    checkStr = array[array.length - 1];
                }
                const index = settings.exportLogExclude.findIndex((val) => {
                    return checkStr.includes(val);
                });
                if (index === -1) {
                    data.push(html);
                }
            }
        }
        if (data.length === 0) {
            return;
        }
        const fileName = `log-${Utils.replaceUnsafeStr(Tools.getPageTitle())}-${Utils.replaceUnsafeStr(DateFormat.format(store.crawlCompleteTime, settings.dateFormat))}.html`;
        const content = `<!DOCTYPE html>
<html>
<body>
<div id="logWrap">
${data.join('\n')}
</div>
</body>
</html>`;
        const blob = new Blob([content], {
            type: 'text/html',
        });
        const url = URL.createObjectURL(blob);
        Utils.downloadFile(url, fileName);
        const msg = lang.transl('_');
        log.success(msg);
        toast.success(msg, {
            position: 'topCenter',
        });
    }
}
const log = new Log();
export { log };
