//  bookmark_detail 
import { InitPageBase } from '../crawl/InitPageBase';
import { Colors } from '../Colors';
import { Tools } from '../Tools';
import { options } from '../setting/Options';
import { API } from '../API';
import { store } from '../store/Store';
class InitBookmarkDetailPage extends InitPageBase {
    constructor() {
        super();
        this.init();
    }
    addCrawlBtns() {
        Tools.addBtn('crawlBtns', Colors.bgBlue, '_', '_').addEventListener('click', () => {
            this.readyCrawl();
        }, false);
    }
    initAny() { }
    setFormOption() {
        // /
        options.setWantPageTip({
            text: '_',
            tip: '_',
            rangTip: `1 - ${this.maxCount}`,
        });
    }
    getWantPage() {
        this.crawlNumber = this.checkWantPageInputGreater0(this.maxCount, false);
    }
    // 
    async getIdList() {
        let data = await API.getRecommenderData(Tools.getIllustId(), this.crawlNumber);
        for (const id of data.recommendations) {
            store.idList.push({
                type: 'unknown',
                id: id.toString(),
            });
        }
        this.getIdListFinished();
    }
}
export { InitBookmarkDetailPage };
