import { EVT } from '../EVT';
import { lang } from '../Lang';
import { log } from '../Log';
import { settings } from '../setting/Settings';
import { IndexedDB } from '../utils/IndexedDB';
import { store } from '../store/Store';
import { fileName } from '../FileName';
import { Utils } from '../utils/Utils';
import { toast } from '../Toast';
import { msgBox } from '../MsgBox';
import { secretSignal } from '../utils/SecretSignal';
// 
class DownloadRecord {
    constructor() {
        this.DBName = 'DLRecord';
        this.DBVer = 1;
        this.storeNameList = [
            'record1',
            'record2',
            'record3',
            'record4',
            'record5',
            'record6',
            'record7',
            'record8',
            'record9',
        ]; // 
        this.existedIdList = []; //  id  put  add
        //  url 
        this.dateRegExp = /img\/(.*)\//;
        this.IDB = new IndexedDB();
        this.init();
    }
    async init() {
        await this.initDB();
        this.bindEvents();
    }
    // 
    async initDB() {
        // 
        const onUpdate = (db) => {
            for (const name of this.storeNameList) {
                if (!db.objectStoreNames.contains(name)) {
                    const store = db.createObjectStore(name, { keyPath: 'id' });
                    store.createIndex('id', 'id', { unique: true });
                }
            }
        };
        return new Promise(async (resolve, reject) => {
            resolve(await this.IDB.open(this.DBName, this.DBVer, onUpdate));
        });
    }
    bindEvents() {
        // 
        window.addEventListener(EVT.list.downloadSuccess, (ev) => {
            const successData = ev.detail.data;
            const result = store.findResult(successData.id);
            result && this.addRecord(result);
        });
        //  id  txt 
        secretSignal.register('recordtxt', () => {
            this.importRecordFromTxt();
        });
        // 
        {
            const btn = document.querySelector('#importDownloadRecord');
            if (btn) {
                btn.addEventListener('click', () => {
                    EVT.fire('importDownloadRecord');
                });
            }
        }
        // 
        window.addEventListener(EVT.list.importDownloadRecord, () => {
            this.importRecordFromJSON();
        });
        // 
        {
            const btn = document.querySelector('#exportDownloadRecord');
            if (btn) {
                btn.addEventListener('click', () => {
                    EVT.fire('exportDownloadRecord');
                });
            }
        }
        // 
        window.addEventListener(EVT.list.exportDownloadRecord, () => {
            this.exportRecord();
        });
        // 
        {
            const btn = document.querySelector('#clearDownloadRecord');
            if (btn) {
                btn.addEventListener('click', () => {
                    EVT.fire('clearDownloadRecord');
                });
            }
        }
        // 
        window.addEventListener(EVT.list.clearDownloadRecord, () => {
            this.clearRecords();
            this.existedIdList = [];
        });
    }
    //  id  storeName
    getStoreName(id) {
        const firstNum = parseInt(id[0]);
        return this.storeNameList[firstNum - 1];
    }
    // 
    createRecord(data) {
        let result = undefined;
        if (typeof data === 'string') {
            result = store.findResult(data);
        }
        else {
            result = data;
        }
        if (result === undefined) {
            throw new Error('createRecord failed');
        }
        return {
            id: result.id,
            n: fileName.createFileName(result),
            d: this.getDateString(result),
        };
    }
    /** */
    getDateString(result) {
        //  uploadDate uploadDate 
        //  URL 
        // 'https://i.pximg.net/img-original/img/2021/10/11/00/00/06/93364702_p0.png'
        // 
        // '2021/10/11/00/00/06'
        // 
        if (result.type !== 3) {
            return result.original.match(this.dateRegExp)[1];
        }
        else {
            //  uploadDate
            // '2021-09-03T14:31:03+00:00'
            return result.uploadDate;
        }
    }
    // 
    async addRecord(result) {
        const storeName = this.getStoreName(result.id);
        const record = this.createRecord(result);
        if (this.existedIdList.includes(result.id)) {
            this.IDB.put(storeName, record);
        }
        else {
            // 
            const result = await this.IDB.get(storeName, record.id);
            this.IDB[result ? 'put' : 'add'](storeName, record);
        }
    }
    /** 
     *
     *  true false 
     */
    async checkDeduplication(result) {
        if (!Utils.isPixiv()) {
            return false;
        }
        return new Promise(async (resolve, reject) => {
            // 
            if (!settings.deduplication) {
                return resolve(false);
            }
            // 
            const storeName = this.getStoreName(result.id);
            const data = (await this.IDB.get(storeName, result.id));
            if (data === null) {
                return resolve(false);
            }
            // 
            this.existedIdList.push(data.id);
            // 
            // 
            if (data.d !== undefined && data.d !== this.getDateString(result)) {
                return resolve(false);
            }
            // 
            // 
            if (data.d === undefined) {
                this.addRecord(result);
            }
            // 
            if (settings.dupliStrategy === 'loose') {
                // 
                return resolve(true);
            }
            else {
                // 
                const name = fileName.createFileName(result);
                return resolve(name === data.n);
            }
        });
    }
    // 
    async clearRecords() {
        if (window.confirm(lang.transl('')) === false) {
            return;
        }
        log.log(lang.transl('_'));
        toast.show(lang.transl('_'));
        let total = this.storeNameList.length;
        let num = 0;
        for (const name of this.storeNameList) {
            log.log(`${lang.transl('_')} ${num}/${total}`);
            num++;
            await this.IDB.clear(name);
        }
        log.log(`${lang.transl('_')} ${num}/${total}`);
        log.success(lang.transl('_'));
        toast.success(lang.transl('_'));
    }
    // 
    async exportRecord() {
        log.log(lang.transl('_'));
        toast.show(lang.transl('_'));
        let total = this.storeNameList.length;
        let num = 0;
        let record = [];
        for (const name of this.storeNameList) {
            log.log(`${lang.transl('_')} ${num}/${total}`);
            num++;
            const r = (await this.IDB.getAll(name));
            record = record.concat(r);
        }
        log.log(`${lang.transl('_')} ${num}/${total}`);
        if (record.length === 0) {
            log.error(lang.transl('_'));
            toast.error(lang.transl('_'));
            return;
        }
        const blob = Utils.json2BlobSafe(record);
        const url = URL.createObjectURL(blob);
        Utils.downloadFile(url, `record-${Utils.replaceUnsafeStr(new Date().toLocaleString())}.json`);
        log.success(lang.transl('_'));
        toast.success(lang.transl('_'));
    }
    // 
    async importRecord(record) {
        var _a, _b;
        log.log(lang.transl('_'));
        // 
        let stored = 0;
        let total = record.length;
        if (total > 10000) {
            log.warning(lang.transl('_'));
        }
        log.log(`${stored}/${total}`, 1, false);
        console.time('importRecord');
        // 
        for (let index = 0; index < this.storeNameList.length; index++) {
            // 
            const data = [];
            for (const r of record) {
                if (parseInt(r.id[0]) - 1 === index) {
                    data.push(r);
                }
            }
            if (data.length === 0) {
                continue;
            }
            // 
            log.log(`${lang.transl('_')} ${data.length}`);
            try {
                // console.time('restoreRecord' + (index + 1))
                await this.IDB.batchAddData(this.storeNameList[index], data, 'id');
                // console.timeEnd('restoreRecord' + (index + 1))
                stored += data.length;
                log.log(`${stored}/${total}`, 1, false);
            }
            catch (error) {
                const errorMsg = (_b = (_a = error) === null || _a === void 0 ? void 0 : _a.target) === null || _b === void 0 ? void 0 : _b.error;
                const tip = errorMsg ? errorMsg : error;
                log.error(tip);
                msgBox.error(tip);
            }
        }
        console.timeEnd('importRecord');
        if (stored < total) {
            return;
        }
        log.success(lang.transl('_'));
        toast.success(lang.transl('_'));
        msgBox.success(lang.transl('_'), {
            title: lang.transl('_'),
        });
        //  100000  30 
    }
    //  json 
    async importRecordFromJSON() {
        const record = (await Utils.loadJSONFile().catch((err) => {
            msgBox.error(err);
            return;
        }));
        if (!record) {
            return;
        }
        // 
        if (Array.isArray(record) === false ||
            record[0].id === undefined ||
            record[0].n === undefined) {
            return msgBox.error(lang.transl('_'));
        }
        this.importRecord(record);
    }
    //  txt 
    //  id
    async importRecordFromTxt() {
        const file = (await Utils.selectFile('.txt'))[0];
        const text = await file.text();
        // 
        let split = '\r\n';
        if (!text.includes(split)) {
            split = '\n';
        }
        const arr = text.split(split);
        //  id
        const record = [];
        for (const str of arr) {
            if (str) {
                record.push({
                    id: str,
                    n: str,
                });
            }
        }
        this.importRecord(record);
    }
}
const downloadRecord = new DownloadRecord();
export { downloadRecord };
